import { Component, Input, OnInit } from '@angular/core';
import { FormControl, NG_VALUE_ACCESSOR } from '@angular/forms';
import { ValueAccessor } from '../../form-builder';
import { IFormControlComponent } from '@helix/platform/shared/api';
import { IDateFormControlOptions } from '../date-form-control';
import { RxDatetimePickerMode, RxDatetimeStruct } from '@bmc-ux/adapt-angular';
import moment from 'moment-es6';
import { isNull } from 'lodash';

@Component({
  selector: 'rx-time-form-control',
  templateUrl: './time-form-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: TimeFormControlComponent,
      multi: true
    }
  ]
})
export class TimeFormControlComponent extends ValueAccessor<string> implements IFormControlComponent, OnInit {
  @Input()
  options: IDateFormControlOptions;

  @Input()
  isDisabled: boolean;

  datePickerControl = new FormControl('');

  pickerMode = RxDatetimePickerMode.Time;

  ngOnInit(): void {
    this.datePickerControl.valueChanges.subscribe((value: RxDatetimeStruct) => {
      if (value && !isNull(value.hours) && !isNull(value.minutes)) {
        this.value = moment({
          hour: value.hours,
          minute: value.minutes,
          seconds: value.seconds || 0
        }).format('HH:mm:ss');
      } else {
        this.value = null;
      }
    });
  }

  writeValue(value: string): void {
    super.writeValue(value);

    const date = moment(this.value, 'LTS');

    if (date.isValid()) {
      this.datePickerControl.setValue({
        hours: date.hour(),
        minutes: date.minutes()
      });
    } else {
      this.datePickerControl.setValue(null);
    }
  }
}
