import {
  Component,
  ElementRef,
  EventEmitter,
  Injector,
  Input,
  OnInit,
  Output,
  QueryList,
  ViewChildren
} from '@angular/core';
import { FormControl, NG_VALUE_ACCESSOR, NgControl } from '@angular/forms';
import { AdaptButtonComponent } from '@bmc-ux/adapt-angular';
import { ValueAccessor } from '../form-builder/value-accessor';

@Component({
  selector: 'rx-boolean',
  templateUrl: './boolean.component.html',
  styleUrls: ['./boolean.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: RxBooleanComponent,
      multi: true
    }
  ]
})
export class RxBooleanComponent extends ValueAccessor<boolean> implements OnInit {
  @Input()
  shouldDisplayAsCheckbox: boolean;

  @Input()
  required: boolean;

  @Input()
  isDisabled: boolean;

  @Input()
  label: string;

  @Input()
  tooltip: string;

  @Output()
  rxBlur = new EventEmitter<FocusEvent>();

  @ViewChildren(AdaptButtonComponent, { read: ElementRef })
  buttons: QueryList<ElementRef>;

  control: FormControl;

  constructor(private injector: Injector) {
    super();
  }

  ngOnInit(): void {
    this.control = this.injector.get(NgControl).control as FormControl;
  }

  getButtonType(value: boolean): string {
    return value === this.value ? 'primary' : 'secondary';
  }

  onButtonBlur(event: FocusEvent): void {
    if (!event.relatedTarget || !this.buttons.find((element) => element.nativeElement === event.relatedTarget)) {
      this.onTouched();
      this.rxBlur.emit(event);
    }
  }

  setValue(value: boolean): void {
    if (this.value === value) {
      this.value = null;
    } else {
      this.value = value;
    }
  }
}
