import { Component } from '@angular/core';
import { RxCurrentUserService, RxNotificationService } from '@helix/platform/shared/api';
import { ActiveModalRef } from '@bmc-ux/adapt-angular';
import {
  RecordInstance,
  RX_RECORD_DEFINITION,
  RxRecordInstanceDataPageService,
  RxRecordInstanceService,
  RxRecordInstanceUpdateService
} from '@helix/platform/record/api';
import { RX_USER_AVAILABILITY } from './user-availability.constants';
import { finalize, switchMap, tap } from 'rxjs/operators';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'rx-user-availability',
  templateUrl: './user-availability.component.html'
})
export class RxUserAvailabilityComponent {
  isUserAvailableForAssignment = this.rxCurrentUserService.isAvailableForAssignment();
  isSaveInProgress = false;

  constructor(
    private rxCurrentUserService: RxCurrentUserService,
    private activeModalRef: ActiveModalRef,
    private rxRecordInstanceDataPageService: RxRecordInstanceDataPageService,
    private rxRecordInstanceService: RxRecordInstanceService,
    private rxRecordInstanceUpdateService: RxRecordInstanceUpdateService,
    private rxNotificationService: RxNotificationService,
    private translateService: TranslateService
  ) {}

  updateAssignmentAvailability(): void {
    this.isSaveInProgress = true;

    this.rxRecordInstanceDataPageService
      .post({
        params: {
          recorddefinition: RX_USER_AVAILABILITY.ctmPeople.recordDefinitionName,
          queryExpression:
            "('" +
            RX_USER_AVAILABILITY.ctmPeople.fieldIds.loginName +
            '\' = "' +
            this.rxCurrentUserService.getName() +
            '")',
          propertySelection: [RX_RECORD_DEFINITION.coreFieldIds.id]
        }
      })
      .pipe(
        switchMap((dataPageResult) =>
          this.rxRecordInstanceService.get(
            RX_USER_AVAILABILITY.ctmPeople.recordDefinitionName,
            dataPageResult.data[0][RX_RECORD_DEFINITION.coreFieldIds.id]
          )
        ),
        tap((recordInstance: RecordInstance) =>
          recordInstance.setFieldValue(
            RX_USER_AVAILABILITY.ctmPeople.fieldIds.assignmentAvailability,
            this.isUserAvailableForAssignment ? '0' : '1'
          )
        ),
        switchMap((recordInstance: RecordInstance) => this.rxRecordInstanceUpdateService.execute(recordInstance)),
        finalize(() => (this.isSaveInProgress = false))
      )
      .subscribe(() => {
        this.rxCurrentUserService.setAssignmentAvailability(this.isUserAvailableForAssignment);
        this.activeModalRef.close();

        this.rxNotificationService.addSuccessMessage(
          this.translateService.instant(
            'com.bmc.arsys.rx.client.shell.my-availability-dialog.available-for-assignment-saved.message'
          )
        );
      });
  }

  cancel(): void {
    this.activeModalRef.close();
  }
}
