import { Component, Input, ViewChild } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { AdaptButtonComponent, RxSelectionChangeEvent } from '@bmc-ux/adapt-angular';
import { IFormControlComponent, IFormFocusable } from '@helix/platform/shared/api';
import { RxModalService } from '@helix/platform/ui-kit';
import { TranslateService } from '@ngx-translate/core';
import { find, forEach, isNil, map, noop, pullAllBy } from 'lodash';
import { ValueAccessor } from '../form-builder';
import { ISelectOption } from '../form-controls/select-form-control';
import { SelectionFieldOptionsEditorComponent } from './selection-field-options-editor.component';
import { ISelectionFieldOptionProperties, ISelectionOption } from './selection-field-options.interface';

@Component({
  selector: 'rx-selection-field-options',
  templateUrl: './selection-field-options.component.html',
  styleUrls: ['./selection-field-options.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: SelectionFieldOptionsComponent,
      multi: true
    }
  ]
})
export class SelectionFieldOptionsComponent
  extends ValueAccessor<ISelectionFieldOptionProperties>
  implements IFormControlComponent, IFormFocusable
{
  @Input()
  options: ISelectionFieldOptionProperties;

  @ViewChild(AdaptButtonComponent)
  adaptButtonComponent: AdaptButtonComponent;

  currentOptions: ISelectionOption[] = [];
  defaultOption: ISelectionOption[] = [];

  constructor(private rxModalService: RxModalService, private translateService: TranslateService) {
    super();
  }

  onWriteValue(currentOptionProperties: ISelectionFieldOptionProperties): void {
    const currentOptionsById = currentOptionProperties?.optionNamesById || this.options.optionNamesById;
    const currentLabelsById = currentOptionProperties?.optionLabelsById || this.options.optionLabelsById;

    this.currentOptions = map(currentOptionsById, (name, id) => ({
      name,
      id: Number(id),
      stringKey: currentLabelsById[id]
    }));

    this.defaultOption = !isNil(currentOptionProperties?.defaultValue)
      ? [find(this.currentOptions, { id: Number(currentOptionProperties.defaultValue) })]
      : [];
  }

  openOptionsEditor(): void {
    this.rxModalService
      .openModal({
        title: this.translateService.instant('com.bmc.arsys.rx.client.selection-field-options-editor.title'),
        content: SelectionFieldOptionsEditorComponent,
        data: {
          existingOptions: this.currentOptions,
          isReadOnly: this.options.isReadOnly
        }
      })
      .then((response: Omit<ISelectionFieldOptionProperties, 'defaultValue'>) => {
        this.value = {
          defaultValue: this.value?.defaultValue,
          optionNamesById: response.optionNamesById,
          optionLabelsById: response.optionLabelsById
        };

        setTimeout(() => {
          this.validateDefaultValue();
        });
      })
      .catch(noop);
  }

  private fetchValue(options: ISelectionOption[]): ISelectionFieldOptionProperties {
    const optionNamesById = {};
    const optionKeysById = {};

    forEach(options, (option: ISelectionOption) => {
      optionNamesById[option.id] = option.name;
    });

    forEach(options, (option: ISelectionOption) => {
      optionKeysById[option.id] = option.stringKey;
    });

    return { defaultValue: this.value?.defaultValue, optionNamesById, optionLabelsById: optionKeysById };
  }

  private validateDefaultValue(): void {
    if (!isNil(this.value?.defaultValue) && !find(this.currentOptions, { id: this.value.defaultValue })) {
      this.defaultOption = [];

      this.value = {
        ...this.value,
        defaultValue: null
      };
    }
  }

  onSelectionChange(event: RxSelectionChangeEvent): void {
    this.value = {
      ...this.value,
      defaultValue: event.options[0]?.id ?? null
    };
  }

  focus(): void {
    this.adaptButtonComponent.elem.nativeElement.focus();
    this.openOptionsEditor();
  }

  optionFormatter(option: ISelectOption): string {
    return option.name;
  }

  removeOption(option: ISelectionOption): void {
    pullAllBy(this.currentOptions, [option], 'id');
    this.validateDefaultValue();
    this.value = this.fetchValue(this.currentOptions);
  }
}
