import {
  Component,
  DoCheck,
  EventEmitter,
  Injector,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
  TemplateRef,
  ViewChild
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR, NgControl, NgModel } from '@angular/forms';
import { AdaptRxSelectComponent, RxSelectOption, SelectModelFormat } from '@bmc-ux/adapt-angular';
import { SelectSettings } from '@bmc-ux/obsolete';
import { once } from 'lodash';
import { Observable, Subject } from 'rxjs';
import { debounceTime, finalize, map, startWith, switchMap, take, tap } from 'rxjs/operators';
import { IRxSelectWithPaginationOption, IRxSelectWithPaginationOptionsPage } from './select-with-pagination.interfaces';

@Component({
  selector: 'rx-select-with-pagination',
  templateUrl: './select-with-pagination.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: RxSelectWithPaginationComponent,
      multi: true
    }
  ]
})
export class RxSelectWithPaginationComponent implements ControlValueAccessor, OnInit, DoCheck, OnChanges, OnDestroy {
  loadMoreCallbackFunc = this.onFilterValueChange.bind(this);

  selectedValue: IRxSelectWithPaginationOption[] = [];
  isOptionLoadingInProgress = false;
  isFirstOptionPageLoaded = false;
  showLoadMoreButton = true;
  formControl: NgControl;

  adaptSelectSettings: SelectSettings = {
    enableSearch: true,
    showUncheckAll: true,
    modelFormat: SelectModelFormat.object,
    placement: 'auto'
  };

  private onFirstTimeDropdownOpen = once(this.onFilterValueChange);
  private onViewValueChange: Function;
  private onTouched: Function;
  private lastFilterValue: string;
  private startIndex = 0;
  private adaptSelectDefaultTitle: string;
  private adaptSelectOptions: IRxSelectWithPaginationOption[] = [];
  private filterValue$ = new Subject<string>();

  options$ = this.filterValue$.pipe(
    tap(() => {
      this.isOptionLoadingInProgress = true;
    }),
    debounceTime(250),
    switchMap((query) => this.loadOptions(query)),
    startWith([])
  );

  @ViewChild(AdaptRxSelectComponent, { static: true, read: NgModel })
  ngModel: NgModel;

  @ViewChild(AdaptRxSelectComponent, { static: true })
  adaptSelectComponent: AdaptRxSelectComponent;

  @Input()
  label = '';

  @Input()
  required = false;

  @Input()
  isMultiSelectionMode = false;

  @Input()
  optionLoader: (
    startIndex: number,
    pageSize: number,
    searchQuery: string
  ) => Observable<IRxSelectWithPaginationOptionsPage>;

  @Input()
  pageSize = 50;

  @Input()
  showDefaultTitle = true;

  @Input()
  showUncheckAll = true;

  @Input()
  readonly = false;

  @Input()
  template: TemplateRef<any>;

  @Output()
  toggleDropdown: EventEmitter<boolean> = new EventEmitter();

  @Output()
  selectionChange: EventEmitter<RxSelectOption> = new EventEmitter<RxSelectOption>();

  @Input()
  viewToModelValueAdapter = (viewValue: IRxSelectWithPaginationOption[]): any => viewValue;

  @Input()
  modelToViewValueAdapter = (modelValue: any): IRxSelectWithPaginationOption[] => modelValue;

  @Input()
  optionFormatter = (option: IRxSelectWithPaginationOption): string => option.displayValue;

  constructor(private injector: Injector) {}

  ngOnInit(): void {
    // cannot inject NgControl instance directly due to the angular circular dependency error.
    // see: https://github.com/angular/components/pull/13860/commits/f6b179e02b33c058a018ce4ccc51932d1416331f
    this.formControl = this.injector.get(NgControl);

    this.adaptSelectSettings = {
      ...this.adaptSelectSettings,
      showUncheckAll: this.showUncheckAll,
      pageSize: this.pageSize
    };

    if (this.isMultiSelectionMode) {
      this.adaptSelectSettings.checkedStyle = 'checkbox';
      this.adaptSelectSettings.showTooltip = true;
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.showDefaultTitle && this.adaptSelectComponent.texts) {
      if (changes.showDefaultTitle.isFirstChange()) {
        this.adaptSelectDefaultTitle = this.adaptSelectComponent.texts.defaultTitle;
        this.adaptSelectComponent.texts.defaultTitle = null;
      }

      this.adaptSelectComponent.texts = {
        ...this.adaptSelectComponent.texts,
        defaultTitle: changes.showDefaultTitle.currentValue ? this.adaptSelectDefaultTitle : null
      };
    }
  }

  resetLoadedOptions(): void {
    this.onFirstTimeDropdownOpen = once(this.onFilterValueChange);
    this.lastFilterValue = null;
    this.isFirstOptionPageLoaded = false;
    this.adaptSelectOptions = [];
  }

  onFilterValueChange(filterValue: string = this.lastFilterValue || ''): void {
    this.filterValue$.next(filterValue);
  }

  private loadOptions(filterValue: string = this.lastFilterValue || ''): Observable<IRxSelectWithPaginationOption[]> {
    if (this.lastFilterValue === filterValue) {
      this.startIndex = this.pageSize > 0 ? this.startIndex + this.pageSize : 0;
    } else {
      this.lastFilterValue = filterValue;

      this.startIndex = 0;
    }

    return this.optionLoader(this.startIndex, this.pageSize, this.lastFilterValue).pipe(
      take(1),
      tap((optionsPage: IRxSelectWithPaginationOptionsPage) => {
        const options = optionsPage.options;

        if (this.startIndex === 0) {
          this.adaptSelectOptions = options;
          this.isFirstOptionPageLoaded = true;
        } else {
          this.adaptSelectOptions = this.adaptSelectOptions.concat(options);
        }

        this.showLoadMoreButton = optionsPage.totalSize > this.adaptSelectOptions.length;
      }),
      map(() => this.adaptSelectOptions),
      finalize(() => {
        this.isOptionLoadingInProgress = false;
      })
    );
  }

  writeValue(selectedValue: any): void {
    this.selectedValue = this.modelToViewValueAdapter(selectedValue);
  }

  onSelectedValueChange(selectedValue): void {
    this.onViewValueChange(this.viewToModelValueAdapter(selectedValue));
  }

  ngDoCheck(): void {
    if (this.formControl.untouched && this.ngModel.touched) {
      this.ngModel.control.markAsUntouched();
    } else if (this.formControl.touched && this.ngModel.untouched) {
      this.ngModel.control.markAsTouched();
    }

    if (this.formControl.pristine && this.ngModel.control.dirty) {
      this.ngModel.control.markAsPristine();
    }
  }

  registerOnChange(fn: any): void {
    this.onViewValueChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  onDropdownToggle(isOpen: boolean): void {
    if (isOpen) {
      this.onFirstTimeDropdownOpen();
      this.toggleDropdown.next(true);
    } else {
      this.onTouched();
      this.toggleDropdown.next(false);

      if (this.lastFilterValue) {
        this.onFilterValueChange('');
      }
    }
  }

  ngOnDestroy(): void {
    this.filterValue$.complete();
  }
}
