import { Component, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { head, isNull } from 'lodash';
import { IFormControlComponent } from '@helix/platform/shared/api';
import { ValueAccessor } from '../../form-builder/value-accessor';
import { ISelectOption } from '../select-form-control';
import { IOptionalSelectFormControlOptions } from './optional-select-form-control.interfaces';

@Component({
  selector: 'rx-optional-select-form-control',
  templateUrl: './optional-select-form-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: OptionalSelectFormControlComponent,
      multi: true
    }
  ]
})
export class OptionalSelectFormControlComponent extends ValueAccessor<string> implements IFormControlComponent {
  @Input()
  options: IOptionalSelectFormControlOptions;

  switcherValue = false;
  selectValue: ISelectOption[] = [];

  onSwitcherChange(newValue: boolean): void {
    this.switcherValue = newValue;

    if (newValue) {
      const option = head(this.options.options);

      if (option && !this.value) {
        this.value = option.id;
      }
    } else {
      this.value = null;
    }
  }

  onWriteValue(value: string): void {
    this.switcherValue = !isNull(value);
    this.selectValue = this.value ? [this.options.options.find((option) => option.id === value)] : [];
  }

  onSelectionChange(value: ISelectOption[]): void {
    this.value = head(value).id;
  }

  optionFormatter(option: ISelectOption): string {
    return option.name;
  }
}
