import {
  Component,
  ElementRef,
  EventEmitter,
  HostBinding,
  HostListener,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { IWindowMessage, RxJsonParserService } from '@helix/platform/utils';
import { IIframeApi } from './iframe.interface';

@Component({
  selector: 'rx-iframe',
  templateUrl: './iframe.component.html',
  styleUrls: ['./iframe.component.scss']
})
export class RxIframeComponent implements OnChanges {
  @Input()
  url: string;

  @HostBinding('class')
  class = 'd-block';

  @HostBinding('class.position-absolute')
  isAbsolutePositioned = false;

  @HostBinding('class.isHidden')
  isHidden = false;

  @Output()
  rxMessage = new EventEmitter<IWindowMessage>();

  @ViewChild('frame')
  iframe: ElementRef;

  src: SafeResourceUrl;

  api: IIframeApi = {
    postMessageToFrame: (message: IWindowMessage): void => {
      this.iframe.nativeElement.contentWindow.postMessage(message, '*');
    }
  };

  @HostListener('window:message', ['$event'])
  onMessage(event: MessageEvent): void {
    if (event.data) {
      const windowMessage = this.rxJsonParserService.tryParseJson<IWindowMessage>(event.data);

      if (windowMessage) {
        this.rxMessage.emit(windowMessage);
      }
    }
  }

  constructor(private rxJsonParserService: RxJsonParserService, private domSanitizer: DomSanitizer) {}

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.url?.currentValue) {
      this.src = this.domSanitizer.bypassSecurityTrustResourceUrl(changes.url.currentValue);
    }
  }

  updateUrl(url: string): void {
    this.iframe.nativeElement.contentWindow.location.replace(url);
  }
}
