import { Component, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { FormControl, NG_VALUE_ACCESSOR, NgControl } from '@angular/forms';
import { IExpressionConfiguratorProvider } from '@helix/platform/shared/api';
import { IExpressionInspectorControlOptions } from './expression-inspector-control-options.interface';
import { InspectorControlBase } from '../../inspector/inspector-control-base.class';
import { IExpressionFormControlOptions } from '../../form-controls/expression-form-control/expression-form-control.types';
import { ExpressionFormControlComponent } from '../../form-controls';

@Component({
  selector: 'rx-expression-inspector-form-control',
  templateUrl: './expression-inspector-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: ExpressionFormControlComponent,
      multi: true
    }
  ]
})
export class ExpressionInspectorControlComponent
  extends InspectorControlBase<string, IExpressionInspectorControlOptions, IExpressionConfiguratorProvider>
  implements OnInit, OnChanges
{
  formControl = this.injector.get(NgControl).control as FormControl;
  expressionFormControlOptions: IExpressionFormControlOptions;

  ngOnInit(): void {
    this.patchOptions();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.optinos) {
      this.patchOptions();
    }
  }

  private patchOptions(): void {
    this.expressionFormControlOptions = {
      ...this.options,
      dataDictionary$: this.designerItemModel.expressionConfigurator.getDataDictionary(this.propertyPath),
      operators: this.designerItemModel.expressionConfigurator.getOperators(this.propertyPath)
    };
  }
}
