import { FORM_BUILDER } from '../form-builder.constant';
import { AbstractFormControlModel } from './abstract-form-control.model';
import { IFormWidgetOptions } from './interfaces/form-widget-options.interface';

export class FormWidgetModel extends AbstractFormControlModel {
  type = FORM_BUILDER.controlTypes.widget;
  component: any;
  hidden = false;
  isDisabled = false;
  options: any;
  widgetName: string;

  constructor(options: IFormWidgetOptions) {
    super();

    this.component = options.component;
    this.hidden = options.hidden;
    this.isDisabled = options.isDisabled;
    this.options = options.options || {};
    this.widgetName = options.widgetName;
  }
}
