import { Component, EventEmitter, Input, OnChanges, OnDestroy, OnInit, Output, SimpleChanges } from '@angular/core';
import {
  FormBuilderEvent,
  IFormBuilderEvent,
  IFormWidgetComponent,
  RX_OVERLAY,
  RxOverlayService
} from '@helix/platform/shared/api';
import { ReplaySubject } from 'rxjs';
import { RX_MODAL, RxModalService } from '@helix/platform/ui-kit';
import { RX_REVERT_CUSTOMIZATION } from './revert-customization.constants';
import { IRevertCustomizationOptions } from './revert-customization.interfaces';

@Component({
  selector: 'rx-revert-customization',
  templateUrl: './revert-customization.component.html'
})
export class RxRevertCustomizationComponent implements OnInit, OnDestroy, OnChanges, IFormWidgetComponent {
  @Input()
  options: IRevertCustomizationOptions;

  @Input()
  isDisabled: boolean;

  @Output()
  events: EventEmitter<IFormBuilderEvent> = new EventEmitter();

  showOverlayOptions = false;
  isRevertActionHidden = false;
  isOverlayOperationHidden = false;
  overlayOperation: string;

  private destroyed$ = new ReplaySubject<boolean>(1);

  constructor(private rxModalService: RxModalService, private rxOverlayService: RxOverlayService) {}

  ngOnInit() {
    this.updateData();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (
      changes.options &&
      (changes.options.currentValue.overlayGroupId !== changes.options.previousValue?.overlayGroupId ||
        changes.options.currentValue.overlayDescriptor?.parentOverlayGroupId !==
          changes.options.previousValue?.overlayDescriptor?.parentOverlayGroupId)
    ) {
      this.updateData();
    }
  }

  ngOnDestroy() {
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }

  private updateData(): void {
    if (this.options.overlayGroupId) {
      this.overlayOperation = this.getOverlayOperation();
      this.isOverlayOperationHidden = RX_OVERLAY.operationTypes.createdInThisOverlayGroup === this.overlayOperation;
      this.isRevertActionHidden = this.shouldHideRevertAction();
      this.showOverlayOptions = !this.isOverlayOperationHidden || !this.isRevertActionHidden;
    } else {
      this.overlayOperation = RX_OVERLAY.operationTypes.createdInThisOverlayGroup;
      this.isOverlayOperationHidden = true;
      this.isRevertActionHidden = true;
    }

    this.events.emit({ type: FormBuilderEvent.HideWidget, payload: !this.showOverlayOptions });
  }

  private revertCustomization(): void {
    this.events.emit({ type: RX_REVERT_CUSTOMIZATION.events.revertCustomization });
  }

  private getOverlayOperation(): string {
    return this.rxOverlayService.getOverlayOperation(
      this.options.overlayGroupId,
      this.options.overlayDescriptor ? this.options.overlayDescriptor.parentOverlayGroupId : null
    );
  }

  private shouldHideRevertAction(): boolean {
    return this.overlayOperation === RX_OVERLAY.operationTypes.customizedInThisOverlayGroup
      ? !this.options.allowOverlay
      : true;
  }

  onRevertCustomization(): void {
    this.rxModalService
      .confirm({
        title: 'Warning',
        modalStyle: RX_MODAL.modalStyles.warning,
        message: 'Are you sure you want to revert customization?'
      })
      .then((revert: boolean) => {
        if (revert) {
          this.revertCustomization();
        }
      });
  }
}
