import { Component, Injector, OnDestroy, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import {
  AdaptModalService,
  AdaptNavigationComponent,
  AdaptNavigationService,
  NavigationAction
} from '@bmc-ux/adapt-angular';
import { RxNotificationService, RxSmartReportingService, RxUpgradeTrackerService } from '@helix/platform/shared/api';
import { RX_MODAL, RxModalService } from '@helix/platform/ui-kit';
import {
  IRxShellConfig,
  IRxShellMenuItem,
  RX_SHELL,
  RX_VIEW_ACTION,
  RxShellService,
  RxViewActionService
} from '@helix/platform/view/api';
import { find, noop } from 'lodash';
import { Observable, Subscription } from 'rxjs';
import { map, shareReplay, switchMap, take, tap } from 'rxjs/operators';
import { RxUserPreferencesComponent } from '../user-preferences/user-preferences.component';
import { ShellBase } from './shell-base.class';
import { IRxShellUpgradeTrackerAction } from './shell-component.types';
import { RxUserAvailabilityComponent } from '../user-availability/user-availability.component';
import { RxApplicationContext } from './gainsight/gainsight.types';
import { GainsightOptInComponent } from './gainsight/gainsight-opt-in/gainsight-opt-in.component';

@Component({
  selector: 'rx-shell',
  templateUrl: './shell.component.html',
  styleUrls: ['./shell.component.scss']
})
export class RxShellComponent extends ShellBase implements OnInit, OnDestroy {
  @ViewChild('adaptNavigation', { static: true })
  protected adaptNavigation: AdaptNavigationComponent;

  @ViewChild('userMessagesPopover', { static: true })
  protected userMessagesPopover: TemplateRef<any>;

  private subscriptions: Subscription = new Subscription();

  constructor(
    private adaptModalService: AdaptModalService,
    private route: ActivatedRoute,
    private rxModalService: RxModalService,
    private rxNotificationService: RxNotificationService,
    private rxShellService: RxShellService,
    private rxSmartReportingService: RxSmartReportingService,
    private rxUpgradeTrackerService: RxUpgradeTrackerService,
    private rxViewActionService: RxViewActionService,
    protected injector: Injector,
    private adaptNavigationService: AdaptNavigationService
  ) {
    super(injector);

    this.rxShellService.resetMenuItemCount();
  }

  ngOnInit() {
    super.ngOnInit();

    this.subscriptions.add(
      this.rxShellService.navigateToSmartReporting$
        .pipe(switchMap(() => this.rxSmartReportingService.openSmartReporting()))
        .subscribe((isSmartReportingConfigured) => {
          if (!isSmartReportingConfigured) {
            this.rxNotificationService.addErrorMessage(
              this.translateService.instant('com.bmc.arsys.rx.client.shell.smart-reporting-not-configured')
            );
          }
        })
    );

    this.subscriptions.add(
      this.rxShellService.navigateToView$.subscribe((menuItem: IRxShellMenuItem) => this.openView(menuItem))
    );

    this.subscriptions.add(this.rxShellService.openUserPreferences$.subscribe(() => this.openUserPreferences()));

    this.subscriptions.add(
      this.rxShellService.openGainsightPreferences$.subscribe(() => this.openGainsightPreferences())
    );

    this.subscriptions.add(this.rxShellService.openUserAvailability$.subscribe(() => this.openUserAvailability()));
  }

  private onNavigationCanceled(): void {
    if (this.currentMenuItemId !== null) {
      this.adaptNavigation.selectMenuItem(this.currentMenuItemId);
    }
  }

  private openView(menuItem: IRxShellMenuItem): void {
    this.rxViewActionService
      .execute(RX_VIEW_ACTION.viewActionNames.openView, menuItem.openViewParams)
      .pipe(take(1))
      .subscribe({
        error: () => {
          this.onNavigationCanceled();
        }
      });
  }

  private openUserPreferences(): void {
    this.adaptModalService
      .open({
        title: this.translateService.instant('com.bmc.arsys.rx.client.shell.my-preferences.label'),
        content: RxUserPreferencesComponent,
        size: 'sm'
      })
      .then(() => {
        this.rxModalService
          .confirm({
            title: this.translateService.instant('com.bmc.arsys.rx.client.common.warning.label'),
            modalStyle: RX_MODAL.modalStyles.warning,
            message: this.translateService.instant(
              'com.bmc.arsys.rx.client.shell.user-preferences-dialog.signout.confirmation.message'
            )
          })
          .then((isLogoutRequested: boolean) => {
            if (isLogoutRequested) {
              this.logout();
            }
          });
      })
      .catch(noop);
  }

  openGainsightPreferences(): void {
    this.adaptModalService
      .open({
        title: this.translateService.instant('com.bmc.arsys.rx.client.shell.analytics.label'),
        content: GainsightOptInComponent,
        size: 'sm'
      })
      .catch(noop);
  }

  private openUserAvailability(): void {
    this.adaptModalService
      .open({
        title: this.translateService.instant('com.bmc.arsys.rx.client.shell.my-availability.label'),
        content: RxUserAvailabilityComponent,
        size: 'sm'
      })
      .catch(noop);
  }

  protected getNavigationActionItems(): Observable<NavigationAction[]> {
    return this.shellConfig$.pipe(
      map((shellConfig) => {
        const actions = [];

        shellConfig.navigationActions.forEach((item) => {
          actions.push({
            action: item.action,
            className: item.className,
            name: item.name,
            tooltip: item.name,
            hide: item.hide
          });
        });

        if (!shellConfig.globalSearchDisabled) {
          actions.push({
            name: this.translateService.instant('com.bmc.arsys.rx.client.common.search.label'),
            tooltip: this.translateService.instant('com.bmc.arsys.rx.client.common.search.label'),
            className: 'd-icon-search',
            route: `/${this.rxGlobalCacheService.applicationId}/search`
          });
        }

        const showUpgradeTrackerAction: IRxShellUpgradeTrackerAction = {
          action: () => {
            this.rxUpgradeTrackerService.displayUpgradeNotification(true);
          },
          className: 'd-icon-clock_alert',
          hide: !this.rxUpgradeTrackerService.isUpgradeInProgress,
          name: ''
        };

        this.subscriptions.add(
          this.rxUpgradeTrackerService.isUpgradeInProgress$.subscribe(() => {
            showUpgradeTrackerAction.hide = !this.rxUpgradeTrackerService.isUpgradeInProgress;
          })
        );

        actions.push(showUpgradeTrackerAction);

        return actions;
      })
    );
  }

  protected getActiveNavigationMenuItem(): IRxShellMenuItem {
    return find(
      this.flattenedMenuItems,
      (menuItem) =>
        menuItem.hide !== true &&
        menuItem.type === RX_SHELL.actions.navigateToView &&
        decodeURIComponent(`/${this.rxGlobalCacheService.applicationId}/view/${menuItem.viewUrl}`) ===
          decodeURIComponent(this.router.url)
    );
  }

  protected getNavigationMenuItems(): Observable<IRxShellMenuItem[]> {
    return this.shellConfig$.pipe(map((shellConfig) => shellConfig.navigationBarItems));
  }

  protected getUserMenuItems(): Observable<IRxShellMenuItem[]> {
    return this.shellConfig$.pipe(map((shellConfig) => shellConfig.userMenu.subMenu));
  }

  ngOnDestroy() {
    super.ngOnDestroy();

    this.subscriptions.unsubscribe();
  }

  protected getShellConfig(): Observable<IRxShellConfig> {
    return this.applicationDescriptor$.pipe(
      switchMap((currentBundleDescriptor) => this.rxShellService.getShellConfig(currentBundleDescriptor.id)),
      tap((shellConfig) => {
        this.flattenedMenuItems = shellConfig.flattenedMenuItems;
      }),
      shareReplay(1)
    );
  }
}
