import {
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  Inject,
  OnDestroy,
  QueryList,
  ViewChild,
  ViewChildren
} from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { FormControl } from '@angular/forms';
import { BehaviorSubject, combineLatest, fromEvent, Observable, ReplaySubject } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  filter,
  map,
  startWith,
  take,
  takeUntil,
  tap,
  withLatestFrom
} from 'rxjs/operators';
import { UP_ARROW, DOWN_ARROW, LEFT_ARROW, RIGHT_ARROW, ENTER } from '@angular/cdk/keycodes';
import { ActiveModalRef, DismissReasons } from '@bmc-ux/adapt-angular';
import { isNumber } from 'lodash';

import { IIconBrowserDialogConfig, IIconGlyph } from '../icon-picker.types';

@Component({
  selector: 'rx-icon-browser-dialog',
  templateUrl: './icon-browser-dialog.component.html',
  styleUrls: ['./icon-browser-dialog.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class IconBrowserDialogComponent implements AfterViewInit, OnDestroy {
  config: IIconBrowserDialogConfig = this.activeModalRef.getData();

  searchFormControl = new FormControl();

  selectedIcon$ = new BehaviorSubject<IIconGlyph>(this.config.selectedIcon);

  filteredIcons$: Observable<IIconGlyph[]> = this.searchFormControl.valueChanges.pipe(
    debounceTime(200),
    startWith(''),
    distinctUntilChanged(),
    map((query) =>
      query
        ? this.config.icons.filter(({ name }) => name.toLowerCase().indexOf(query.toLowerCase()) > -1)
        : this.config.icons
    )
  );

  isSelectButtonDisabled$ = combineLatest([this.selectedIcon$, this.filteredIcons$]).pipe(
    map(
      ([selectedIcon, filteredIcons]) =>
        !selectedIcon || !filteredIcons.length || selectedIcon === this.config.selectedIcon
    )
  );

  @ViewChildren('iconButton', { read: ElementRef })
  iconButtons: QueryList<ElementRef<HTMLButtonElement>>;

  @ViewChild('iconsList', { read: ElementRef })
  iconsList: ElementRef<HTMLUListElement>;

  private selectedIndex$: Observable<number> = combineLatest([this.selectedIcon$, this.filteredIcons$]).pipe(
    map(([selectedIcon, filteredIcons]) =>
      selectedIcon ? filteredIcons.findIndex((icon) => icon.id === selectedIcon.id) : null
    ),
    distinctUntilChanged()
  );

  private destroyed$ = new ReplaySubject<boolean>(1);

  constructor(private activeModalRef: ActiveModalRef, @Inject(DOCUMENT) private document: any) {}

  ngAfterViewInit(): void {
    fromEvent<KeyboardEvent>(this.iconsList.nativeElement, 'keydown')
      .pipe(
        filter((event) => [UP_ARROW, DOWN_ARROW, LEFT_ARROW, RIGHT_ARROW, ENTER].includes(event.keyCode)),
        tap((event) => event.preventDefault()),
        withLatestFrom(this.filteredIcons$, this.selectedIndex$),
        takeUntil(this.destroyed$)
      )
      .subscribe(([event, icons, selectedIndex]) => {
        // we have to subtract 1px from the icon button width to compensate -1 margin trick in the CSS
        const cellsPerLine = Math.floor(
          this.iconsList.nativeElement.clientWidth / (this.iconButtons.first.nativeElement.offsetWidth - 1)
        );

        let newIndex: number;

        if ([UP_ARROW, DOWN_ARROW, LEFT_ARROW, RIGHT_ARROW].includes(event.keyCode)) {
          if (isNumber(selectedIndex)) {
            switch (event.keyCode) {
              case LEFT_ARROW: {
                newIndex = selectedIndex > 0 ? selectedIndex - 1 : 0;
                break;
              }

              case UP_ARROW: {
                newIndex = selectedIndex - cellsPerLine;

                if (newIndex < 0) {
                  newIndex = selectedIndex;
                }

                break;
              }

              case RIGHT_ARROW: {
                newIndex = selectedIndex < icons.length - 1 ? selectedIndex + 1 : selectedIndex;
                break;
              }

              case DOWN_ARROW: {
                newIndex = selectedIndex + cellsPerLine;

                if (newIndex >= icons.length) {
                  newIndex = selectedIndex;
                }

                break;
              }
            }
          } else {
            newIndex = 0;
          }

          this.selectIcon(icons[newIndex]);

          this.scrollToSelectedIcon();
        } else {
          event.preventDefault();

          if (this.selectedIcon$.value) {
            this.onSelect();
          }
        }
      });

    this.scrollToSelectedIcon();
  }

  ngOnDestroy(): void {
    this.destroyed$.next();
    this.destroyed$.complete();

    this.selectedIcon$.complete();
  }

  selectIcon(icon: IIconGlyph): void {
    this.selectedIcon$.next(icon);
  }

  onSelect(): void {
    this.activeModalRef.close(this.selectedIcon$.value);
  }

  onCancel(): void {
    this.activeModalRef.dismiss(DismissReasons.CLOSE_BTN);
  }

  isIconSelected(icon: IIconGlyph): boolean {
    return this.selectedIcon$.value?.id === icon.id;
  }

  private scrollToSelectedIcon(): void {
    this.selectedIcon$.pipe(take(1)).subscribe((selectedIcon) => {
      if (selectedIcon) {
        this.iconsList.nativeElement.querySelector(`.d-icon-${selectedIcon.id}`)?.scrollIntoView({
          block: 'nearest',
          inline: 'nearest'
        });
      }
    });
  }

  trackByFn(index: number, item: IIconGlyph): string {
    return item.id;
  }
}
