import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { FormControl, NG_VALUE_ACCESSOR } from '@angular/forms';
import { RxDateStruct, RxDatetimePickerMode } from '@bmc-ux/adapt-angular';
import moment from 'moment-es6';
import { IFormControlComponent } from '@helix/platform/shared/api';
import { ValueAccessor } from '../../form-builder/value-accessor';
import { IDateFormControlOptions } from './date-form-control-options.interface';

@Component({
  selector: 'rx-date-form-control',
  templateUrl: './date-form-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: DateFormControlComponent,
      multi: true
    }
  ]
})
export class DateFormControlComponent extends ValueAccessor<string> implements IFormControlComponent, OnInit {
  @Input()
  options: IDateFormControlOptions;

  @Input()
  isDisabled: boolean;

  datePickerControl = new FormControl('');

  pickerMode = RxDatetimePickerMode.Date;

  ngOnInit(): void {
    this.datePickerControl.valueChanges.subscribe((value: RxDateStruct) => {
      if (value) {
        this.value = moment([value.year, value.month, value.date]).format('YYYY-MM-DD');
      } else {
        this.value = null;
      }
    });
  }

  writeValue(value: string): void {
    super.writeValue(value);

    const date = moment(this.value);

    if (date.isValid()) {
      this.datePickerControl.setValue({
        year: date.year(),
        month: date.month(),
        date: date.date()
      });
    } else {
      this.datePickerControl.setValue(null);
    }
  }
}
