import { Component, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { FormControl, NG_VALUE_ACCESSOR, NgControl } from '@angular/forms';
import { IExpressionConfiguratorProvider } from '@helix/platform/shared/api';
import { InspectorControlBase } from '../../inspector/inspector-control-base.class';
import { IOptionalExpressionInspectorControlOptions } from './optional-expression-inspector-control-options.interface';
import { IOptionalExpressionControlOptions } from '../../form-controls';

@Component({
  selector: 'rx-optional-expression-inspector-control',
  templateUrl: './optional-expression-inspector-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: OptionalExpressionInspectorControlComponent,
      multi: true
    }
  ]
})
export class OptionalExpressionInspectorControlComponent
  extends InspectorControlBase<string, IOptionalExpressionInspectorControlOptions, IExpressionConfiguratorProvider>
  implements OnInit, OnChanges
{
  formControl = this.injector.get(NgControl).control as FormControl;
  optionalExpressionFormControlOptions: IOptionalExpressionControlOptions;

  ngOnInit(): void {
    this.patchOptions();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.optinos) {
      this.patchOptions();
    }
  }

  private patchOptions(): void {
    this.optionalExpressionFormControlOptions = {
      ...this.options,
      dataDictionary$: this.designerItemModel.expressionConfigurator.getDataDictionary(this.propertyPath),
      operators: this.designerItemModel.expressionConfigurator.getOperators(this.propertyPath)
    };
  }
}
