import { Component, EventEmitter, HostListener, Input, OnChanges, Output, SimpleChanges } from '@angular/core';
import { AdaptTreeNode } from '@bmc-ux/adapt-angular';
import { isArray, map as _map, uniqueId, flow, compact } from 'lodash';
import { IDataDictionary } from '@helix/platform/shared/api';

@Component({
  selector: 'rx-data-dictionary',
  templateUrl: './data-dictionary.component.html',
  styleUrls: ['./data-dictionary.component.scss']
})
export class DataDictionaryComponent implements OnChanges {
  @Input()
  dataDictionary: IDataDictionary;

  @Output()
  nodeSelected = new EventEmitter();

  @Output()
  dragStart = new EventEmitter<any>();

  tree: AdaptTreeNode[];

  @HostListener('dragstart', ['$event'])
  onDragStart(event: DragEvent) {
    const value = (event.target as Element).getAttribute('rx-expression');

    if (value) {
      event.dataTransfer.setData('value', value);
    } else {
      event.preventDefault();
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.dataDictionary) {
      this.tree = this.prepareTreeForAdapt(this.dataDictionary);
    }
  }

  onNodeExpand(e) {}

  onNodeSelected(node: AdaptTreeNode): void {
    this.nodeSelected.next(node);
  }

  private prepareTreeForAdapt(dataDictionary: IDataDictionary): AdaptTreeNode[] {
    return flow(
      (dictionary) =>
        _map(dictionary, (element) =>
          !element.hidden && (element.expression || element.children?.length)
            ? {
                label: element.label,
                children: this.prepareTreeForAdapt(element.children),
                expanded: element.expanded,
                data: {
                  expression: isArray(element.expression) ? element.expression[0] : element.expression,
                  icon: element.icon,
                  info: element.info
                },
                key: uniqueId(),
                draggable: Boolean(element.expression)
              }
            : null
        ),
      (dictionary) => compact(dictionary)
    )(dataDictionary);
  }
}
