import { Component, Input, Renderer2, ViewChild } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { IFormControlComponent, IFormFocusable } from '@helix/platform/shared/api';
import { ValueAccessor } from '../../form-builder/value-accessor';
import { ITextFormControlOptions } from './text-form-control-options.interface';
import { AdaptRxTextfieldComponent } from '@bmc-ux/adapt-angular';

@Component({
  selector: 'rx-text-form-control',
  templateUrl: './text-form-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: TextFormControlComponent,
      multi: true
    }
  ]
})
export class TextFormControlComponent extends ValueAccessor<string> implements IFormControlComponent, IFormFocusable {
  @Input()
  options: ITextFormControlOptions;

  @ViewChild('editor', { static: true })
  editor: AdaptRxTextfieldComponent;

  constructor(private renderer: Renderer2) {
    super();
  }

  focus() {
    this.renderer.selectRootElement(this.editor.inputRef.nativeElement, true).focus();
  }
}
