import { Component, Input, NgZone, OnInit } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { FileObj } from '@bmc-ux/adapt-angular';
import { IFormControlComponent } from '@helix/platform/shared/api';
import { saveAs } from 'file-saver';
import { map } from 'lodash';
import { ValueAccessor } from '../../form-builder/value-accessor';
import { IAttachmentFormControlOptions } from './attachment-form-control-options.interface';

@Component({
  selector: 'rx-attachment-form-control',
  templateUrl: './attachment-form-control.component.html',
  styleUrls: ['./attachment-form-control.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: AttachmentFormControlComponent,
      multi: true
    }
  ]
})
export class AttachmentFormControlComponent extends ValueAccessor<File[]> implements IFormControlComponent, OnInit {
  @Input()
  options: IAttachmentFormControlOptions;

  @Input()
  isDisabled: boolean;

  fileObjects: FileObj[];
  customDownload = this.downloadFile.bind(this);

  constructor(private ngZone: NgZone) {
    super();
  }

  ngOnInit(): void {
    if (!this.options.maxFileSize) {
      this.options.maxFileSize = Number.MAX_SAFE_INTEGER.toString();
    }

    if (!this.options.filesCount) {
      this.options.filesCount = '1';
    }
  }

  onModelChange(files: FileObj[]): void {
    this.value = files.length ? files.map((file) => file.data) : null;
  }

  onWriteValue(value: File[]): void {
    this.fileObjects = map(value, (fileData) => {
      const defaultFileObject = {
        data: fileData,
        allowDeletion: true,
        inUploading: false,
        inDeleting: false,
        uploaded: 100,
        error: false,
        errorText: ''
      };

      const updatedFileObject = (this.fileObjects || []).find(({ data }) => data === fileData) || defaultFileObject;

      updatedFileObject.uploaded = 100;

      return updatedFileObject;
    });
  }

  private downloadFile(fileObj: FileObj): void {
    this.ngZone.runOutsideAngular(() => {
      saveAs(fileObj.data, fileObj.data.name);
    });
  }
}
