import { Component, Injector, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActiveModalRef, DismissReasons } from '@bmc-ux/adapt-angular';
import { ReplaySubject } from 'rxjs';

import { RxIssueReporterService } from './issue-reporter.service';
import { takeUntil } from 'rxjs/operators';
import { IServerResponseMessage, RxNotificationService } from '@helix/platform/shared/api';
import { TranslateService } from '@ngx-translate/core';
import { RxModalClass } from '@helix/platform/ui-kit';

@Component({
  selector: 'rx-issue-reporter',
  templateUrl: './issue-reporter.component.html'
})
export class RxIssueReporterComponent extends RxModalClass implements OnDestroy {
  private destroyed$ = new ReplaySubject<boolean>(1);

  data: IServerResponseMessage;
  reportForm: FormGroup;

  constructor(
    private activeModalRef: ActiveModalRef,
    private formBuilder: FormBuilder,
    private rxIssueReporterService: RxIssueReporterService,
    private rxNotificationService: RxNotificationService,
    private translateService: TranslateService,
    protected injector: Injector
  ) {
    super(activeModalRef, injector);
    this.data = this.activeModalRef.getData();
    this.initForm();
  }

  isDirty(): boolean {
    return this.reportForm.dirty;
  }

  reportIssue(): void {
    this.rxIssueReporterService
      .reportIssue(this.reportForm.get('text').value, this.data)
      .pipe(takeUntil(this.destroyed$))
      .subscribe(() => {
        this.rxNotificationService.addSuccessMessage(
          this.translateService.instant('com.bmc.arsys.rx.client.issue-reporter.issue-report-submitted.message')
        );

        this.activeModalRef.close();
      });
  }

  private initForm(): void {
    this.reportForm = this.formBuilder.group({
      text: ['', Validators.required]
    });
  }

  cancel(): void {
    this.activeModalRef.dismiss(DismissReasons.CLOSE_BTN);
  }

  ngOnDestroy(): void {
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }
}
