import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { RxSelectOption } from '@bmc-ux/adapt-angular';
import { head, last, upperFirst, includes } from 'lodash';
import { IFormControlComponent } from '@helix/platform/shared/api';
import { ValueAccessor } from '../../form-builder/value-accessor';
import { IOptionalExpressionControlOptions } from './optional-expression-control-options.interface';
import { IExpressionFormControlOptions } from '../expression-form-control';

@Component({
  selector: 'rx-optional-expression-form-control',
  templateUrl: './optional-expression-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: OptionalExpressionControlComponent,
      multi: true
    }
  ],
  styleUrls: ['./optional-expression-control.component.scss']
})
export class OptionalExpressionControlComponent extends ValueAccessor<string> implements IFormControlComponent, OnInit {
  @Input()
  options: IOptionalExpressionControlOptions;

  @Input()
  propertyPath: string;

  @Output()
  events: EventEmitter<any> = new EventEmitter();

  expressionFieldOptions: IExpressionFormControlOptions;

  checkbox: boolean;

  modelValues = {
    disable: '0',
    enable: '1'
  };

  selectValues = {
    all: 'all',
    condition: 'condition'
  };

  selectOptions: RxSelectOption[] = [
    {
      id: this.selectValues.all,
      name: 'At all times'
    },
    {
      id: this.selectValues.condition,
      name: 'When condition is true'
    }
  ];

  selectedCondition: RxSelectOption[] = [head(this.selectOptions)];

  conditionValue: string;

  ngOnInit(): void {
    this.expressionFieldOptions = {
      label: 'Condition',
      expressionEditorPropertyName: this.options.expressionEditorPropertyName || upperFirst(this.propertyPath),
      dataDictionary$: this.options.dataDictionary$,
      operators: this.options.operators
    };
  }

  onWriteValue(modelValue: string): void {
    this.conditionValue = '';
    this.selectedCondition = [head(this.selectOptions)];

    if (modelValue === this.modelValues.enable) {
      this.checkbox = true;
    } else if (modelValue === this.modelValues.disable) {
      this.checkbox = false;
    } else {
      this.checkbox = true;
      this.selectedCondition = [last(this.selectOptions)];
      this.conditionValue = modelValue;
    }
  }

  onSwitcherChange(modelValue: boolean): void {
    this.selectedCondition = [head(this.selectOptions)];
    this.conditionValue = '';

    this.value = modelValue ? this.modelValues.enable : this.modelValues.disable;
  }

  onSelectChange(selectedValue: RxSelectOption[]): void {
    this.selectedCondition = selectedValue;
    this.conditionValue = '';

    this.value = head(selectedValue).id === this.selectValues.all ? this.modelValues.enable : '';
  }

  onConditionChange(expressionValue: string): void {
    this.value = expressionValue;
  }

  optionFormatter(option: RxSelectOption): string {
    return option.name;
  }
}
