import { Component, Injector, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ActiveModalRef, DismissReasons } from '@bmc-ux/adapt-angular';
import { RxModalClass } from '@helix/platform/ui-kit';
import { RxGuidService } from '@helix/platform/utils';
import { TranslateService } from '@ngx-translate/core';
import { filter, find, forEach, lowerCase, map, max, some, trim } from 'lodash';
import { ISelectionOption } from './selection-field-options.interface';

@Component({
  selector: 'rx-selection-field-options-editor',
  templateUrl: './selection-field-options-editor.component.html',
  styleUrls: ['./selection-field-options-editor.component.scss']
})
export class SelectionFieldOptionsEditorComponent extends RxModalClass {
  @ViewChild('optionSelectionForm')
  optionSelectionForm: NgForm;

  isReadOnly = this.activeModalRef.getData().isReadOnly;

  selectionOptions: ISelectionOption[] = map(
    this.activeModalRef.getData().existingOptions,
    (option: ISelectionOption) => ({
      name: option.name,
      id: option.id,
      stringKey: option.stringKey,
      isOpen: false
    })
  );

  private duplicateOptionNameMsg = this.translateService.instant(
    'com.bmc.arsys.rx.client.selection-field-options-editor.duplicate-option-name.error'
  );

  private duplicateOptionIdMsg = this.translateService.instant(
    'com.bmc.arsys.rx.client.designer.validation.duplicate-value.message'
  );

  constructor(
    private activeModalRef: ActiveModalRef,
    protected injector: Injector,
    private rxGuidService: RxGuidService,
    private translateService: TranslateService
  ) {
    super(activeModalRef, injector);
  }

  isDirty(): boolean {
    return this.optionSelectionForm?.dirty;
  }

  saveOptions(): void {
    const optionNamesById = {};
    const optionKeysById = {};

    forEach(this.selectionOptions, (option: ISelectionOption) => {
      optionNamesById[option.id] = option.name;
    });

    forEach(this.selectionOptions, (option: ISelectionOption) => {
      optionKeysById[option.id] = option.stringKey;
    });

    this.activeModalRef.close({
      optionNamesById,
      optionLabelsById: optionKeysById
    });
  }

  addOption(): void {
    let name;
    let counter = 0;
    let existingOption;

    const newOptionLabel = this.translateService.instant(
      'com.bmc.arsys.rx.client.selection-field-options-editor.new-option.label'
    );

    do {
      name = counter === 0 ? newOptionLabel : `${newOptionLabel} ` + counter;
      counter++;
      existingOption = find(this.selectionOptions, { name });
    } while (existingOption);

    this.selectionOptions.push({
      name,
      id: this.selectionOptions.length
        ? max(this.selectionOptions.map((option: ISelectionOption) => option.id)) + 10
        : 0,
      stringKey: this.rxGuidService.generate(),
      isOpen: true
    });

    this.optionSelectionForm.form.markAsDirty();
  }

  removeOption(index: number): void {
    this.selectionOptions.splice(index, 1);
    this.optionSelectionForm.form.markAsDirty();
    this.validateIdAndNames('name');
    this.validateIdAndNames('id');
  }

  expandAll(): void {
    this.selectionOptions.forEach((option: ISelectionOption) => (option.isOpen = true));
  }

  collapseAll(): void {
    this.selectionOptions.forEach((option: ISelectionOption) => (option.isOpen = false));
  }

  cancel(): void {
    this.activeModalRef.dismiss(DismissReasons.CLOSE_BTN);
  }

  private getDuplicateOptions(type: 'name' | 'id') {
    return filter(
      this.selectionOptions,
      (option: ISelectionOption) =>
        filter(this.selectionOptions, (item: ISelectionOption) =>
          type === 'name' ? lowerCase(item.name) === trim(lowerCase(option.name)) : item.id === option.id
        ).length > 1
    );
  }

  validateIdAndNames(type: 'name' | 'id'): void {
    if (this.optionSelectionForm.invalid) {
      return;
    }

    if (type === 'name') {
      forEach(this.selectionOptions, (option: ISelectionOption) => {
        option.invalidNameError = null;
      });

      this.getDuplicateOptions(type).forEach((option) => {
        option.invalidNameError = this.duplicateOptionNameMsg;
      });
    }

    if (type === 'id') {
      forEach(this.selectionOptions, (option: ISelectionOption) => {
        option.invalidIdError = null;
      });

      this.getDuplicateOptions(type).forEach((option) => {
        option.invalidIdError = this.duplicateOptionIdMsg;
      });
    }
  }

  isSaveButtonDisabled(): boolean {
    return (
      !this.optionSelectionForm?.dirty ||
      this.optionSelectionForm?.invalid ||
      some(this.selectionOptions, (option: ISelectionOption) => option.invalidNameError || option.invalidIdError)
    );
  }

  trackByIndex(index: number): number {
    return index;
  }
}
