import { Component, Input, Renderer2, ViewChild } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { IFormControlComponent, IFormFocusable } from '@helix/platform/shared/api';
import { ValueAccessor } from '../../form-builder/value-accessor';
import { ITagsFormControlOptions, ITagsFormControlModel, ITagAutocompleteValue } from './tags-form-control.types';
import { AdaptMetatagComponent, InitTagEditingEvent } from '@bmc-ux/adapt-angular';
import { isEmpty, isObject } from 'lodash';
import { Observable } from 'rxjs';
import { debounceTime, distinctUntilChanged, map } from 'rxjs/operators';
import { RxStringService } from '@helix/platform/utils';

@Component({
  selector: 'rx-tags-form-control',
  templateUrl: './tags-form-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: TagsFormControlComponent,
      multi: true
    }
  ],
  styleUrls: ['./tags-form-control.component.scss']
})
export class TagsFormControlComponent extends ValueAccessor<string> implements IFormControlComponent, IFormFocusable {
  @Input()
  options: ITagsFormControlOptions;

  @ViewChild('adaptTagField', { static: true })
  adaptTagField: AdaptMetatagComponent;

  tags: ITagsFormControlModel[];

  autocompleteSearchBound: (text$: Observable<string>) => Observable<ITagAutocompleteValue[]> =
    this.autocompleteSearch.bind(this);

  constructor(private rxStringService: RxStringService, private renderer: Renderer2) {
    super();
  }

  focus(): void {
    // @ts-ignore
    this.renderer.selectRootElement(this.adaptTagField.adaptRxTypeaheadComponent.inputRef.nativeElement, true).focus();
  }

  onTagsModelChange(tags: ITagsFormControlModel[]): void {
    this.tags = tags;

    this.value = this.getStringFromTags(tags);
  }

  onWriteValue(value: string): void {
    this.tags = this.getTagsFromString(value);
  }

  getStringFromTags(tags: ITagsFormControlModel[]): string {
    if (isEmpty(tags)) {
      return null;
    }

    const tagsString = tags.map((tag) => (this.isAutocompleteTag(tag) ? tag.data.value : tag).trim()).join(' ');

    return tagsString.replace(/\s{2,}/g, ' ');
  }

  isAutocompleteTag(tag: ITagsFormControlModel): tag is ITagAutocompleteValue {
    return isObject(tag);
  }

  getTagsFromString(tagsString: string): ITagsFormControlModel[] {
    if (tagsString) {
      const tags = tagsString.split(' ');
      const autocompleteValues = this.options.autocompleteValues;

      if (isEmpty(autocompleteValues)) {
        return tags;
      }

      return tags
        .map((tag) => autocompleteValues.find((autocomplete) => autocomplete.data.value === tag) || tag.trim())
        .filter(Boolean);
    }

    return [];
  }

  autocompleteSearch(text$: Observable<string>): Observable<ITagAutocompleteValue[]> {
    return text$.pipe(
      debounceTime(250),
      distinctUntilChanged(),
      map((searchTerm) => {
        const autocompleteValues = this.options.autocompleteValues;

        if (isEmpty(autocompleteValues)) {
          return [];
        }

        return autocompleteValues.filter((autocompleteValue) =>
          this.rxStringService.caseInsensitiveSearch(autocompleteValue.text, searchTerm)
        );
      })
    );
  }

  onInitTagEditing(event: InitTagEditingEvent): void {
    const tag = event.tag as ITagsFormControlModel;

    if (this.isAutocompleteTag(tag)) {
      event.preventDefault();
    }
  }
}
