import { Component, OnDestroy, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AdaptLazyLoadEvent, ColumnConfig, DataCellTemplateParams } from '@bmc-ux/adapt-table';
import {
  IDataPageParams,
  RxBundleCacheService,
  RxDataPageFactoryService,
  RxPageTitleService
} from '@helix/platform/shared/api';
import { RxShellService } from '@helix/platform/view/api';
import { TranslateService } from '@ngx-translate/core';
import { filter, isEmpty, keyBy, map, sortBy } from 'lodash';
import { combineLatest, Subscription, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { IRxSearchRecord } from './search-record.interface';

interface ISearchResult {
  createDate: string;
  modifyDate: string;
  recordDefinitionName: string;
  recordInstanceDisplayId: string;
  recordInstanceId: string;
  title: string;
  weight: number;
  wordsAroundHit: string;
}

@Component({
  selector: 'rx-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss']
})
export class RxSearchComponent implements OnDestroy, OnInit {
  @ViewChild('colIdCellTemplate', { static: true })
  colIdCellTemplate: TemplateRef<DataCellTemplateParams>;

  @ViewChild('cellTemplate', { static: true })
  cellTemplate: TemplateRef<DataCellTemplateParams>;

  private pageSize = 50;
  private subscription: Subscription;

  loading = true;
  loadingMore = false;
  startIndex = 0;
  totalSize = 0;

  columns: ColumnConfig[];
  globalSearchRecords: IRxSearchRecord[];
  recordDefinitionsByName: { [recordDefinitioName: string]: IRxSearchRecord };
  searchResults: ISearchResult[];
  searchString = '';

  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private rxDataPageService: RxDataPageFactoryService,
    private rxShellService: RxShellService,
    private rxPageTitleService: RxPageTitleService,
    private translateService: TranslateService,
    public rxBundleCacheService: RxBundleCacheService
  ) {}

  ngOnInit(): void {
    this.rxPageTitleService.set(this.translateService.instant('com.bmc.arsys.rx.client.shell.search.results.label'));

    this.subscription = combineLatest([this.activatedRoute.queryParamMap, this.rxShellService.shellConfig$]).subscribe(
      ([queryParams, shellConfig]) => {
        this.searchString = queryParams.get('q') || '';

        this.globalSearchRecords = map(shellConfig.globalSearchRecords, (item) => {
          item.selected = true;

          return item;
        });

        this.recordDefinitionsByName = keyBy(shellConfig.globalSearchRecords, 'name');

        this.loading = false;

        this.getGlobalSearchResults();
      }
    );

    this.columns = [
      {
        field: 'colId',
        width: '45px',
        cellTemplate: this.colIdCellTemplate
      },
      {
        field: 'recordDefinitionName',
        cellTemplate: this.cellTemplate
      }
    ];
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  get searchValue(): string {
    return this.searchString.trim();
  }

  getGlobalSearchResults(infiniteScroll: boolean = false): void {
    this.loading = true;
    this.loadingMore = infiniteScroll;

    if (!infiniteScroll) {
      this.startIndex = 0;
      this.totalSize = 0;
    }

    if (this.searchValue) {
      const queryParams: IDataPageParams = {
        mayHaveText: `%${this.searchValue}%`,
        pageSize: this.pageSize,
        searchResultOption: 'WORDS_AROUND_HIT',
        startIndex: this.startIndex
      };

      if (!isEmpty(this.globalSearchRecords)) {
        queryParams.recordDefinitionNames = map(filter(this.globalSearchRecords, 'selected'), 'name').join(',');
      }

      if (queryParams.recordDefinitionNames) {
        this.rxDataPageService
          .withType('com.bmc.arsys.rx.application.search.datapage.SearchDataPageQuery')
          .get({ params: queryParams })
          .pipe(
            catchError((error) => {
              this.loading = false;
              this.loadingMore = false;

              return throwError(error);
            })
          )
          .subscribe((results) => {
            this.loading = false;
            this.loadingMore = false;

            if (this.totalSize > 0) {
              this.totalSize--;
            }

            this.totalSize += this.pageSize === results.totalSize ? results.totalSize + 1 : results.totalSize;

            const newData = results.data as ISearchResult[];

            this.startIndex += newData.length;

            if (infiniteScroll) {
              this.searchResults = this.searchResults.concat(newData);
            } else {
              this.searchResults = newData;
            }

            this.searchResults = sortBy(this.searchResults, 'weight');
          });
      } else {
        this.resetSearchResults();
      }
    } else {
      this.resetSearchResults();
    }
  }

  private resetSearchResults() {
    this.searchResults = [];
    this.totalSize = 0;
    this.loading = false;
    this.loadingMore = false;
  }

  search(): void {
    const trimmedSearchValue = this.searchString.trim();

    if (trimmedSearchValue) {
      this.router.navigate([], {
        relativeTo: this.activatedRoute,
        queryParams: { q: trimmedSearchValue },
        queryParamsHandling: 'merge'
      });
    }
  }

  onFiltersChanged(): void {
    setTimeout(() => this.getGlobalSearchResults());
  }

  onLazyLoad(event: AdaptLazyLoadEvent): void {
    this.getGlobalSearchResults(true);
  }

  selectAllRecords(): void {
    this.globalSearchRecords.forEach((record) => (record.selected = true));

    this.getGlobalSearchResults();
  }

  unSelectAllRecords(): void {
    this.globalSearchRecords.forEach((record) => (record.selected = false));

    this.getGlobalSearchResults();
  }
}
