import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { IFormControlComponent } from '@helix/platform/shared/api';
import { TranslateService } from '@ngx-translate/core';
import { IDefinitionPickerComponentOptions } from '../../definition-picker';
import { RX_EXPRESSION_EDITOR } from '../../expression-editor';
import { ValueAccessor } from '../../form-builder/value-accessor';
import { IExpressionFormControlOptions } from '../expression-form-control';
import { IDefinitionPickerOrExpressionFormControlOptions } from './definition-picker-or-expression-form-control.types';

enum DefinitionPickerOrExpressionComponentMode {
  Definition = 'Definition',
  Expression = 'Expression'
}

@Component({
  selector: 'rx-definition-picker-or-expression-form-control',
  templateUrl: 'definition-picker-or-expression-form-control.component.html',
  styleUrls: ['definition-picker-or-expression-form-control.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: DefinitionPickerOrExpressionFormControlComponent,
      multi: true
    }
  ]
})
export class DefinitionPickerOrExpressionFormControlComponent
  extends ValueAccessor<string>
  implements OnInit, IFormControlComponent
{
  @Input()
  options: IDefinitionPickerOrExpressionFormControlOptions;

  @Input()
  propertyPath: string;

  @Output()
  events: EventEmitter<any> = new EventEmitter();

  definitionPickerOptions: IDefinitionPickerComponentOptions;
  expressionFormControlOptions: IExpressionFormControlOptions;
  componentMode = DefinitionPickerOrExpressionComponentMode;
  activeMode = DefinitionPickerOrExpressionComponentMode.Definition;

  ngOnInit(): void {
    this.activeMode = this.isDynamicDefinitionName(this.value)
      ? DefinitionPickerOrExpressionComponentMode.Expression
      : DefinitionPickerOrExpressionComponentMode.Definition;

    this.definitionPickerOptions = {
      definitionType: this.options.definitionType,
      label: this.options.label
    };

    this.expressionFormControlOptions = {
      dataDictionary$: this.options.dataDictionary$,
      operators: this.options.operators,
      label: this.options.label
    };
  }

  selectMode(mode: DefinitionPickerOrExpressionComponentMode): void {
    if (this.activeMode !== mode) {
      this.activeMode = mode;
      this.value = null;
    } else if (this.activeMode === mode && this.value) {
      this.events.emit({
        type: RX_EXPRESSION_EDITOR.events.openExpressionEditor,
        payload: {
          propertyPath: this.propertyPath
        }
      });
    }
  }

  onModelValueChange(expressionValue: string): void {
    this.value = expressionValue;
  }

  private isDynamicDefinitionName(definitionName: string): boolean {
    return Boolean(definitionName?.match(/\$\{.*\}/));
  }
}
