import { Component, Input, OnChanges, SimpleChanges } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import {
  ICustomizationControlValue,
  ICustomizationOptions,
  ICustomizationOptionsEditorData,
  IScopeSelectionOption,
  RxBundleCacheService,
  RxOverlayService,
  RX_OVERLAY
} from '@helix/platform/shared/api';
import { RxModalService } from '@helix/platform/ui-kit';
import { noop } from 'lodash';
import { take } from 'rxjs/operators';
import { CoarseGrainedCustomizationOptionsEditorComponent } from '../../coarse-grained-customization-options-editor/coarse-grained-customization-options-editor.component';
import { ValueAccessor } from '../../form-builder/value-accessor';

@Component({
  selector: 'rx-scope-customization-control',
  templateUrl: './customization-options.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: CustomizationOptionsComponent,
      multi: true
    }
  ]
})
export class CustomizationOptionsComponent extends ValueAccessor<ICustomizationControlValue> implements OnChanges {
  @Input()
  options: ICustomizationOptions;

  allowOverlayLabel: string;
  definitionScopeName: string;
  overlayOperation: string;

  private scopeSelectionOptions: IScopeSelectionOption[];

  constructor(
    private rxModalService: RxModalService,
    private rxBundleCacheService: RxBundleCacheService,
    private rxOverlayService: RxOverlayService
  ) {
    super();
  }

  ngOnInit(): void {
    if (this.options) {
      this.updateValues();
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.options) {
      this.updateValues();
    }
  }

  private getOverlayOperation(): string {
    return this.rxOverlayService.getOverlayOperation(
      this.options.overlayGroupId,
      this.options.overlayDescriptor?.parentOverlayGroupId || null
    );
  }

  private updateValues(): void {
    this.setAllowOverlayLabel(this.options.allowOverlay);

    this.rxBundleCacheService
      .getDefinitionScopeName(this.options.scope)
      .pipe(take(1))
      .subscribe((definitionScopeName: string) => (this.definitionScopeName = definitionScopeName));

    this.rxBundleCacheService
      .getDefinitionScopeSelectionOptions()
      .pipe(take(1))
      .subscribe(
        (scopeSelectionOptions: IScopeSelectionOption[]) => (this.scopeSelectionOptions = scopeSelectionOptions)
      );

    if (this.options.overlayGroupId) {
      this.overlayOperation = this.getOverlayOperation();
    } else {
      this.overlayOperation = RX_OVERLAY.operationTypes.createdInThisOverlayGroup;
    }
  }

  openCustomizationOptionsEditor(): void {
    this.rxModalService
      .openModal({
        title: 'Scope/Customization options',
        content: CoarseGrainedCustomizationOptionsEditorComponent,
        blockKeyboard: false,
        size: 'sm',

        data: {
          definitionScopeName: this.definitionScopeName,
          allowOverlay: this.options.allowOverlay,
          scopeSelectionOptions: this.scopeSelectionOptions,
          isDisabled: this.options.isDisabled,
          overlayOperation: this.overlayOperation,
          definitionTypeDisplayName: this.options.definitionTypeDisplayName
        } as ICustomizationOptionsEditorData
      })
      .then((result: ICustomizationControlValue) => {
        this.setAllowOverlayLabel(result.allowOverlay);
        this.definitionScopeName = this.scopeSelectionOptions.find((value) => value.id === result.scope).name;

        this.value = result;
      })
      .catch(noop);
  }

  setAllowOverlayLabel(allowOverlay: boolean): void {
    this.allowOverlayLabel = allowOverlay
      ? RX_OVERLAY.overlayAllowedLabels.allowed
      : RX_OVERLAY.overlayAllowedLabels.notAllowed;
  }
}
