import { ChangeDetectorRef, Component, Input, ViewChild } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { AdaptButtonGroupComponent, ButtonConfig } from '@bmc-ux/adapt-angular';
import { IFormControlComponent } from '@helix/platform/shared/api';
import { ValueAccessor } from '../../form-builder/value-accessor';
import { IGroupButtonFormControlOptions } from './group-button-control-options.interface';
import { findIndex } from 'lodash';

@Component({
  selector: 'rx-group-button-form-control',
  templateUrl: './group-button-form-control.component.html',
  styleUrls: ['./group-button-form-control.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: GroupButtonFormControlComponent,
      multi: true
    }
  ]
})
export class GroupButtonFormControlComponent extends ValueAccessor<string | number> implements IFormControlComponent {
  @ViewChild(AdaptButtonGroupComponent, { static: true })
  adaptButtonGroupComponent: AdaptButtonGroupComponent;

  @Input()
  options: IGroupButtonFormControlOptions;

  model: boolean[];

  constructor(private changeDetectorRef: ChangeDetectorRef) {
    super();
  }

  onGroupButtonChange(groupButtonValues: boolean[]): void {
    const index = findIndex(groupButtonValues, (buttonValue) => buttonValue);

    this.setValue(this.options.items[index]);
  }

  onWriteValue(value: string | number): void {
    this.updateModel(value);
  }

  private updateModel(value: string | number): void {
    this.model = this.options.items.map((item) => item.value === value);
  }

  private setValue(item: ButtonConfig): void {
    if (this.options.beforeValueChange) {
      this.options.beforeValueChange(this.value, item.value).then((response) => {
        if (response) {
          this.value = item.value;
        } else {
          this.updateModel(this.value);
          this.adaptButtonGroupComponent.writeValue(this.model);
          this.changeDetectorRef.detectChanges();
        }
      });
    } else {
      this.value = item.value;
    }
  }
}
