import {
  AfterViewInit,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges
} from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup, ValidatorFn } from '@angular/forms';
import {
  IFormBuilderConfig,
  IFormBuilderEvent,
  IFormControlBuilderConfig,
  IFormWidgetBuilderConfig,
  IPlainObject
} from '@helix/platform/shared/api';
import { RxGuidService } from '@helix/platform/utils';
import { find, forEach, has, isEmpty, isEqual } from 'lodash';
import { Observable, ReplaySubject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { FormBuilderFactory } from '../../form-builder.factory';
import { FormControlModel } from '../../models/form-control.model';
import { FormSectionModel } from '../../models/form-section.model';
import { FormWidgetModel } from '../../models/form-widget.model';
import { IFormBuilderEditor } from './form-builder-editor.interface';
import { FormBuilderService } from './form-builder.service';

@Component({
  selector: 'rx-form-builder',
  templateUrl: './form-builder.component.html',
  providers: [FormBuilderService]
})
export class FormBuilderComponent implements OnInit, AfterViewInit, OnDestroy, OnChanges {
  @Input()
  config: IFormBuilderConfig;

  @Input()
  model: IPlainObject;

  @Input()
  guid: string = this.rxGuidService.generate();

  @Input()
  isReadOnly?: boolean;

  @Input()
  focusEditor$?: Observable<IFormBuilderEditor>;

  @Output()
  modelChange: EventEmitter<any> = new EventEmitter();

  @Output()
  editorEvent: EventEmitter<IFormBuilderEvent> = new EventEmitter();

  @Output()
  formInitialized: EventEmitter<void> = new EventEmitter();

  layout: any;
  formGroup: FormGroup;

  private destroyed$: ReplaySubject<boolean> = new ReplaySubject(1);
  private formSubscription: Subscription;

  constructor(
    private formBuilderFactory: FormBuilderFactory,
    private formBuilder: FormBuilder,
    private rxGuidService: RxGuidService,
    private formBuilderService: FormBuilderService
  ) {
    this.formGroup = this.formBuilder.group({});

    this.subscribeOnFormDataChange();
  }

  ngOnInit(): void {
    if (this.focusEditor$) {
      this.focusEditor$.pipe(takeUntil(this.destroyed$)).subscribe((focusEditorEvent: IFormBuilderEditor) => {
        this.formBuilderService.setFocusEditor(focusEditorEvent);
      });
    }

    this.formBuilderService.editorEvent$.pipe(takeUntil(this.destroyed$)).subscribe((event: IFormBuilderEvent) => {
      this.editorEvent.emit(event);
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (
      changes.model &&
      changes.model.currentValue &&
      !isEqual(changes.model.currentValue, changes.model.previousValue)
    ) {
      this.updateFormData(changes.model.currentValue);
    }

    if (changes.config) {
      this.prepareLayout(changes.config.currentValue || []);
    }

    if (changes.isReadOnly && changes.isReadOnly.isFirstChange() && changes.isReadOnly.currentValue) {
      this.formGroup.disable();
    }
  }

  ngAfterViewInit() {
    this.formInitialized.next();
  }

  trackByControl(index: number, item: FormSectionModel): string {
    return `${this.guid}${item.label}`;
  }

  ngOnDestroy(): void {
    this.formSubscription.unsubscribe();
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }

  dispatch(event: IFormBuilderEvent): void {
    this.formBuilderService.dispatch(event);
  }

  private updateFormData(model): void {
    this.formSubscription.unsubscribe();

    Object.keys(model).forEach((propertyName) => this.getFormControlByName(propertyName).setValue(model[propertyName]));

    Object.keys(this.formGroup.getRawValue())
      .filter((propertyName) => !has(model, propertyName))
      .forEach((propertyName) => this.formGroup.removeControl(propertyName));

    this.subscribeOnFormDataChange();
  }

  private prepareLayout(config: IFormBuilderConfig): void {
    this.layout = {
      controls: config.map((section) =>
        this.formBuilderFactory.section({
          ...section,
          controls: section.controls.map((propertyControlConfig) => {
            let model: FormControlModel | FormWidgetModel;

            if (this.isFormControl(propertyControlConfig)) {
              const control = this.getFormControlByName(propertyControlConfig.name);

              this.isReadOnly || propertyControlConfig.isDisabled
                ? control.disable({ emitEvent: false })
                : control.enable({ emitEvent: false });

              model = this.formBuilderFactory.control(propertyControlConfig, control);
            } else {
              model = this.formBuilderFactory.widget({
                ...propertyControlConfig,
                isDisabled: propertyControlConfig.isDisabled || this.isReadOnly
              });
            }

            return model;
          })
        })
      )
    };
  }

  private getFormControlByName(name: string): AbstractControl {
    let control = this.formGroup.controls[name];

    if (!control) {
      let validators: ValidatorFn[];

      forEach(this.config, (section) => {
        const formControlConfig = find(section.controls, { name }) as IFormControlBuilderConfig;

        if (formControlConfig) {
          validators = formControlConfig.validators;
        }

        return !formControlConfig;
      });

      control = this.formBuilder.control(null, validators || []);

      this.formGroup.addControl(name, control);
    }

    return control;
  }

  private subscribeOnFormDataChange(): void {
    this.formSubscription = this.formGroup.valueChanges.subscribe(() => {
      // Get a raw value for all form controls including disabled.
      const rawValue = this.formGroup.getRawValue();
      // Check if rawValue is not an empty object, in this case we don't
      // want to emit model change when current model value is null.
      const formData = isEmpty(rawValue) ? null : rawValue;

      if (!isEqual(formData, this.model)) {
        this.modelChange.emit(formData);
      }
    });
  }

  private isFormControl(
    config: IFormControlBuilderConfig | IFormWidgetBuilderConfig
  ): config is IFormControlBuilderConfig {
    return !!(config as IFormControlBuilderConfig).name;
  }
}
