import { Component, Input, OnInit, Renderer2, ViewChild } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { debounceTime, distinctUntilChanged, map } from 'rxjs/operators';
import { merge, Observable, OperatorFunction, Subject } from 'rxjs';
import { noop, get, sortBy } from 'lodash';
import { AdaptModalService, AdaptRxTypeaheadComponent, RxTypeaheadSelectItemEvent } from '@bmc-ux/adapt-angular';
import iconFontConfig from '@bmc-ux/dpl-iconfont/config/if_dpl.json';
import { RX_LEGACY_ICONS } from '@helix/platform/view/api';
import { IFormControlComponent } from '@helix/platform/shared/api';
import { IIconGlyph, IIconBrowserDialogConfig, IIconPickerFormControlOptions } from './icon-picker.types';
import { IconBrowserDialogComponent } from './icon-browser-dialog/icon-browser-dialog.component';
import { ValueAccessor } from '../../form-builder/value-accessor';

@Component({
  selector: 'rx-icon-picker-form-control',
  templateUrl: './icon-picker-form-control.component.html',
  styleUrls: ['./icon-picker-form-control.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: IconPickerFormControlComponent,
      multi: true
    }
  ]
})
export class IconPickerFormControlComponent extends ValueAccessor<string> implements OnInit, IFormControlComponent {
  @Input()
  options: IIconPickerFormControlOptions;

  selectedIcon: IIconGlyph;

  private focus$ = new Subject<FocusEvent>();

  private iconGlyphs = iconFontConfig.glyphs as IIconGlyph[];

  @ViewChild(AdaptRxTypeaheadComponent, { static: true })
  typeahead: AdaptRxTypeaheadComponent;

  constructor(private adaptModalService: AdaptModalService, private renderer: Renderer2) {
    super();
  }

  ngOnInit(): void {
    this.iconGlyphs = sortBy(this.iconGlyphs, (glyph) => glyph.name.toLowerCase());
  }

  search: OperatorFunction<string, IIconGlyph[]> = (text$: Observable<string>) => {
    const userInput$ = text$.pipe(debounceTime(200), distinctUntilChanged());

    const inputFocus$ = this.focus$.pipe(map((event) => (event.target as HTMLInputElement).value));

    return merge(userInput$, inputFocus$).pipe(
      map((term) =>
        term
          ? this.iconGlyphs.filter(({ name }) => name.toLowerCase().indexOf(term.toLowerCase()) > -1)
          : this.iconGlyphs
      )
    );
  };

  inputFormatter(option: IIconGlyph): string {
    return option.name;
  }

  onSelectItem(event: RxTypeaheadSelectItemEvent): void {
    this.value = event.item.id;
  }

  onWriteValue(value: string): void {
    const iconId = get(RX_LEGACY_ICONS, value, value);

    if (!this.selectedIcon || this.selectedIcon.id !== iconId) {
      this.selectedIcon = this.iconGlyphs.find((icon) => icon.id === iconId);
    }
  }

  onFocus(event: FocusEvent): void {
    this.focus$.next(event);
  }

  onBlur(): void {
    this.selectedIcon = this.iconGlyphs.find((icon) => icon.id === this.value);

    const inputValue = this.typeahead.inputRef.nativeElement.value;

    if (!inputValue || !this.selectedIcon) {
      this.value = '';

      this.onWriteValue(this.value);

      this.renderer.setProperty(this.typeahead.inputRef.nativeElement, 'value', '');
    }
  }

  openIconBrowserDialog(): void {
    this.adaptModalService
      .open({
        title: 'Select icon',
        data: {
          selectedIcon: this.selectedIcon,
          icons: this.iconGlyphs
        } as IIconBrowserDialogConfig,
        content: IconBrowserDialogComponent
      })
      .then((selectedIcon: IIconGlyph) => {
        this.value = selectedIcon.id;
        this.selectedIcon = selectedIcon;
      })
      .catch(noop);
  }
}
