import { Component, Input, OnInit } from '@angular/core';
import { FormControl, NG_VALUE_ACCESSOR } from '@angular/forms';
import { ValueAccessor } from '../../form-builder';
import { IFormControlComponent } from '@helix/platform/shared/api';
import { IDateFormControlOptions } from '../date-form-control';
import { RxDatetimePickerMode, RxDatetimeStruct } from '@bmc-ux/adapt-angular';
import moment from 'moment-es6';
import { isNull, isUndefined } from 'lodash';

@Component({
  selector: 'rx-date-time-form-control',
  templateUrl: './date-time-form-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: DateTimeFormControlComponent,
      multi: true
    }
  ]
})
export class DateTimeFormControlComponent extends ValueAccessor<string> implements IFormControlComponent, OnInit {
  @Input()
  options: IDateFormControlOptions;

  @Input()
  isDisabled: boolean;

  datePickerControl = new FormControl('');

  pickerMode = RxDatetimePickerMode.DateTime;

  ngOnInit(): void {
    this.datePickerControl.valueChanges.subscribe((value: RxDatetimeStruct) => {
      if (value && !isNull(value.hours) && !isNull(value.minutes)) {
        this.value = moment([
          value.year || 0,
          value.month || 0,
          value.date || 0,
          value.hours,
          value.minutes,
          value.seconds || 0
        ]).toISOString();
      } else {
        this.value = null;
      }
    });
  }

  writeValue(value: string): void {
    super.writeValue(value);

    const date = moment(this.value);

    if (date.isValid()) {
      this.datePickerControl.setValue({
        year: date.year(),
        month: date.month(),
        date: date.date(),
        hours: date.hour(),
        minutes: date.minutes()
      });
    } else {
      this.datePickerControl.setValue(null);
    }
  }
}
