import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { IErrorPageData } from './error-page.interfaces';
import { takeUntil, switchMap, map, tap } from 'rxjs/operators';
import { ReplaySubject, forkJoin } from 'rxjs';
import { DOCUMENT } from '@angular/common';
import {
  RxGlobalCacheService,
  RxCurrentUserService,
  RxAngularApplicationService,
  RX_APPLICATION,
  IBundleDescriptor,
  RxAuthService
} from '@helix/platform/shared/api';
import { remove, sortBy, filter } from 'lodash';

@Component({
  selector: 'rx-error-page',
  templateUrl: './error-page.component.html',
  styleUrls: ['error-page.component.scss']
})
export class RxErrorPageComponent implements OnInit, OnDestroy {
  data: IErrorPageData;
  showSignInLink = false;
  showApplications = false;

  applications$ = this.rxGlobalCacheService.getBundleDescriptors().pipe(
    switchMap((bundleDescriptors: IBundleDescriptor[]) => {
      const applicationBundleDescriptors = filter(bundleDescriptors, { isApplication: true, isLicensed: true });

      remove(applicationBundleDescriptors, { id: RX_APPLICATION.settingsBundleId });

      if (!this.rxCurrentUserService.isAdministrator()) {
        remove(applicationBundleDescriptors, { id: RX_APPLICATION.dataloadBundleId });
      }

      if (!this.rxCurrentUserService.isAdministrator() && !this.rxCurrentUserService.isBusinessAnalyst()) {
        remove(applicationBundleDescriptors, { id: RX_APPLICATION.innovationStudioBundleId });
      }

      const applications = sortBy(
        applicationBundleDescriptors,
        (bundleDescriptor: IBundleDescriptor) => bundleDescriptor.friendlyName
      ).map((app: IBundleDescriptor) =>
        this.rxAngularApplicationService.isAngularJsApplication(app.id).pipe(
          map((isAngularJsApplication: boolean) => {
            let url: string;

            if (app.hasCustomEntryPoint && app.id !== RX_APPLICATION.innovationStudioBundleId) {
              url = `/${app.id}/index.html`;
            } else if (isAngularJsApplication) {
              url = `/innovationsuite/index.html#/${app.id}`;
            } else {
              url = `/helix/index.html#/${app.id}`;
            }

            return {
              id: app.id,
              friendlyName: app.friendlyName,
              url
            };
          })
        )
      );

      return forkJoin(applications);
    }),
    tap(() => (this.rxGlobalCacheService.applicationId = 'unknown-application'))
  );

  private destroyed$ = new ReplaySubject<boolean>(1);

  constructor(
    @Inject(DOCUMENT) private document: any,
    private title: Title,
    private route: ActivatedRoute,
    private rxAuthService: RxAuthService,
    private rxGlobalCacheService: RxGlobalCacheService,
    private rxCurrentUserService: RxCurrentUserService,
    private rxAngularApplicationService: RxAngularApplicationService
  ) {
    this.document.body.style.overflow = 'hidden';
  }

  ngOnInit() {
    this.route.data.pipe(takeUntil(this.destroyed$)).subscribe((data) => {
      this.data = { ...data } as IErrorPageData;
      this.title.setTitle(this.data.errorTitle);
      this.showSignInLink = this.data.showSignInLink;
      this.showApplications = this.data.showApplications ?? false;
    });
  }

  logout(): void {
    this.rxAuthService.logout().subscribe();
  }

  ngOnDestroy(): void {
    this.document.body.style.removeProperty('overflow');
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }
}
