import { Component, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { IFormControlComponent } from '@helix/platform/shared/api';
import { RxColorUtilsService } from '@helix/platform/utils';
import { ValueAccessor } from '../../form-builder/value-accessor';
import { IColorPickerFormControlOptions } from './color-picker-form-control-options.interface';
import { Color } from '@bmc-ux/adapt-angular';

@Component({
  selector: 'rx-color-picker-form-control',
  templateUrl: './color-picker-form-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: ColorPickerFormControlComponent,
      multi: true
    }
  ]
})
export class ColorPickerFormControlComponent extends ValueAccessor<string> implements IFormControlComponent {
  @Input()
  options: IColorPickerFormControlOptions;

  @Input()
  isDisabled: boolean;

  private colorValue: string;

  constructor(private rxColorUtilsService: RxColorUtilsService) {
    super();
  }

  get color(): string {
    return this.colorValue;
  }

  set color(color: string) {
    if (!this.rxColorUtilsService.isSameColor(color, this.value)) {
      this.value = this.rxColorUtilsService.normalize(color);
      this.colorValue = color;
    }
  }

  onWriteValue(value: string): void {
    if (!this.color || !this.rxColorUtilsService.isSameColor(this.color, value)) {
      this.colorValue = this.rxColorUtilsService.normalize(value);
    }
  }

  setColor(color: Color): void {
    this.color = color?.value || 'null';
  }
}
