import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { NgModel } from '@angular/forms';
import { ActiveModalRef, AdaptTreeNode, DismissReasons } from '@bmc-ux/adapt-angular';
import {
  IDataDictionary,
  IExpressionConfigurator,
  IExpressionOperatorRow,
  RxExpressionParserService
} from '@helix/platform/shared/api';
import { RxModalClass } from '@helix/platform/ui-kit';
import { RxError } from '@helix/platform/utils';
import { TranslateService } from '@ngx-translate/core';
import { findIndex, sortBy } from 'lodash';
import { concat, EMPTY, Observable, of, throwError } from 'rxjs';
import { concatMap, finalize, switchMap, switchMapTo, take, tap } from 'rxjs/operators';
import { IExpressionEditorModalConfig } from './expression-editor-modal-config.interface';
import { IExpressionEditorLegendItem, IExpressionEditorProperty } from './expression-editor.interfaces';
import { RichExpressionEditorComponent } from './rich-expression-editor/rich-expression-editor.component';

@Component({
  selector: 'rx-expression-editor',
  templateUrl: './expression-editor.component.html',
  styleUrls: ['./expression-editor.component.scss']
})
export class ExpressionEditorComponent extends RxModalClass implements OnInit {
  get expression(): string {
    return this.currentProperty && this.currentProperty.value;
  }

  set expression(value: string) {
    if (this.currentProperty) {
      this.currentProperty.value = value;
    }
  }

  currentProperty: IExpressionEditorProperty;
  dataDictionary: IDataDictionary;
  legend: IExpressionEditorLegendItem[];
  operatorRows: IExpressionOperatorRow[];

  private isPropertyContextReady = false;
  private config: IExpressionEditorModalConfig = this.activeModalRef.getData();
  private expressionConfigurator: IExpressionConfigurator = this.config.expressionConfigurator;
  private availableExpressionProperties: IExpressionEditorProperty[] = [];

  private invalidExpressionMessage = this.translateService.instant(
    'com.bmc.arsys.rx.client.expression-editor.invalid-expression.message'
  );

  get isReadOnly(): boolean {
    return this.config.isReadOnly;
  }

  get isNavigationEnabled(): boolean {
    return this.isPropertyContextReady && (this.hasPreviousProperty() || this.hasNextProperty());
  }

  get isPreviousButtonDisabled(): boolean {
    return !this.isPropertyContextReady || this.richExpressionEditorControl.invalid || !this.hasPreviousProperty();
  }

  get isNextButtonDisabled(): boolean {
    return !this.isPropertyContextReady || this.richExpressionEditorControl.invalid || !this.hasNextProperty();
  }

  @ViewChild(RichExpressionEditorComponent, { static: true })
  richExpressionEditor: RichExpressionEditorComponent;

  @ViewChild(RichExpressionEditorComponent, { read: NgModel, static: true })
  richExpressionEditorControl: NgModel;

  constructor(
    public activeModalRef: ActiveModalRef,
    private translateService: TranslateService,
    private rxExpressionParserService: RxExpressionParserService,
    protected injector: Injector
  ) {
    super(activeModalRef, injector);
  }

  ngOnInit(): void {
    super.ngOnInit();
    this.initialize(this.config.property).subscribe();
  }

  onSave(): void {
    this.writeValue().subscribe({
      complete: () => {
        this.activeModalRef.close();
      }
    });
  }

  onNodeSelected(node: AdaptTreeNode): void {
    this.richExpressionEditor.insertHtml(node.data.expression);
  }

  navigateToNextProperty(): void {
    this.navigateToProperty(this.getCurrentPropertyIndex() + 1);
  }

  navigateToPreviousProperty(): void {
    this.navigateToProperty(this.getCurrentPropertyIndex() - 1);
  }

  cancelModal(): void {
    this.activeModalRef.dismiss(DismissReasons.CLOSE_BTN);
  }

  isDirty(): boolean {
    return this.richExpressionEditorControl.dirty;
  }

  private navigateToProperty(index: number): void {
    this.isPropertyContextReady = false;

    concat(
      this.writeValue(),
      // getPropertyByIndex must be called when writeValue observable will be
      // completed to make sure availableExpressionProperties are up to date.
      of(index).pipe(concatMap((i) => this.initialize(this.getPropertyByIndex(i))))
    )
      .pipe(
        finalize(() => {
          this.isPropertyContextReady = true;
          this.richExpressionEditorControl.control.markAsPristine();
        })
      )
      .subscribe();
  }

  private initialize(property: IExpressionEditorProperty): Observable<never> {
    return concat(
      this.updateAvailableExpressionProperties(),
      this.expressionConfigurator.getDataDictionary(property.path).pipe(
        tap((dataDictionary) => {
          this.currentProperty = property;
          this.operatorRows = this.expressionConfigurator.getOperatorRows(property.path);
          this.dataDictionary = dataDictionary;
          this.legend = sortBy(this.config.legend, 'label');

          this.isPropertyContextReady = true;
        }),
        take(1),
        switchMapTo(EMPTY)
      )
    );
  }

  private writeValue(): Observable<never> {
    return this.expressionConfigurator.validateProperty(this.currentProperty.path, this.currentProperty.value).pipe(
      tap((isValid) => {
        if (isValid) {
          this.config.api.writeValue(
            this.currentProperty.path,
            this.rxExpressionParserService.stripSpaces(this.currentProperty.value)
          );
        } else {
          this.richExpressionEditorControl.control.setErrors({
            invalidExpression: {
              message: this.invalidExpressionMessage
            }
          });
        }
      }),
      switchMap((isValid) => (isValid ? EMPTY : throwError(new RxError())))
    );
  }

  private updateAvailableExpressionProperties(): Observable<never> {
    return this.config.expressionPropertyNavigator
      ? this.config.expressionPropertyNavigator.getProperties().pipe(
          take(1),
          tap((properties) => {
            this.availableExpressionProperties = properties;
          }),
          switchMapTo(EMPTY)
        )
      : EMPTY;
  }

  private hasPreviousProperty(): boolean {
    const currentIndex = this.getCurrentPropertyIndex();

    return currentIndex > 0;
  }

  private hasNextProperty(): boolean {
    const currentIndex = this.getCurrentPropertyIndex();

    return currentIndex !== this.availableExpressionProperties.length - 1 && currentIndex !== -1;
  }

  private getCurrentPropertyIndex(): number {
    return findIndex(this.availableExpressionProperties, { path: this.currentProperty.path });
  }

  private getPropertyByIndex(propertyIndex: number): IExpressionEditorProperty {
    return this.availableExpressionProperties[propertyIndex];
  }
}
