import { Injectable, Injector, OnDestroy, OnInit, Renderer2, TemplateRef } from '@angular/core';
import { NavigationCancel, NavigationEnd, NavigationError, NavigationStart, Router } from '@angular/router';
import { AdaptNavigationComponent, AdaptTranslateService, NavigationAction, ProfileModel } from '@bmc-ux/adapt-angular';
import { RxRecordInstanceService } from '@helix/platform/record/api';
import {
  RX_APPLICATION,
  RX_USER,
  RxAngularApplicationService,
  RxAuthService,
  RxComponentCanDeactivateGuard,
  RxCurrentUserService,
  RxFeatureService,
  RxGlobalCacheService,
  RxSystemConfigurationService
} from '@helix/platform/shared/api';
import { IRxShellConfig, IRxShellMenuItem } from '@helix/platform/view/api';
import { TranslateService } from '@ngx-translate/core';
import { filter as _filter, find, get, includes, isBoolean, isEmpty, lowerCase, noop, remove, sortBy } from 'lodash';
import {
  combineLatest,
  forkJoin,
  from,
  merge,
  NEVER,
  Observable,
  of,
  ReplaySubject,
  Subject,
  Subscription
} from 'rxjs';
import {
  catchError,
  defaultIfEmpty,
  filter,
  map,
  shareReplay,
  skip,
  switchMap,
  take,
  takeUntil,
  tap,
  withLatestFrom
} from 'rxjs/operators';
import { RxGainsightConfiguratorService } from './gainsight/gainsight-configurator.service';
import { IRxShellApplication } from './shell-component.types';
import { RX_USER_MESSAGE } from './user-messages/user-message.constants';
import { RxUserMessageService } from './user-messages/user-message.service';

@Injectable()
export abstract class ShellBase implements OnInit, OnDestroy {
  protected abstract adaptNavigation: AdaptNavigationComponent;
  protected abstract userMessagesPopover: TemplateRef<any>;

  isShellInitialized = false;

  busySubscription: Subscription;

  protected currentMenuItemId: number = null;
  protected translateService: TranslateService = this.injector.get(TranslateService);
  protected flattenedMenuItems: IRxShellMenuItem[] = [];

  protected rxComponentCanDeactivateGuard: RxComponentCanDeactivateGuard =
    this.injector.get(RxComponentCanDeactivateGuard);

  protected rxUserMessageService: RxUserMessageService = this.injector.get(RxUserMessageService);
  protected rxAuthService: RxAuthService = this.injector.get(RxAuthService);
  protected rxCurrentUserService: RxCurrentUserService = this.injector.get(RxCurrentUserService);
  protected rxRecordInstanceService: RxRecordInstanceService = this.injector.get(RxRecordInstanceService);
  protected rxGlobalCacheService: RxGlobalCacheService = this.injector.get(RxGlobalCacheService);
  protected rxAngularApplicationService: RxAngularApplicationService = this.injector.get(RxAngularApplicationService);
  protected rxSystemConfigurationService: RxSystemConfigurationService =
    this.injector.get(RxSystemConfigurationService);

  protected rxFeatureService: RxFeatureService = this.injector.get(RxFeatureService);
  protected adaptTranslateService: AdaptTranslateService = this.injector.get(AdaptTranslateService);

  protected rxGainsightConfiguratorService: RxGainsightConfiguratorService =
    this.injector.get(RxGainsightConfiguratorService);

  protected router: Router = this.injector.get(Router);

  protected applicationDescriptor$ = this.rxGlobalCacheService.getApplicationBundleDescriptor();

  private destroyed$ = new ReplaySubject<boolean>(1);
  bundleDescriptors: IRxShellApplication[];

  private removeWindowBlurListener: () => void;

  private helixPortalUrl: string;

  private navigationEnd$ = this.router.events.pipe(
    filter<NavigationEnd>((event) => event instanceof NavigationEnd),
    filter((event) => event.url !== '/unknown-application')
  );

  private closeBannerSubject = new Subject<boolean>();

  hasBanner$ = merge(
    this.rxGainsightConfiguratorService.gainsightInitConfiguration$.pipe(
      filter((gainsightConfiguration) => Boolean(gainsightConfiguration)),
      map((gainsightConfiguration) => gainsightConfiguration.displayBanner),
      defaultIfEmpty(false)
    ),
    this.closeBannerSubject.pipe(map((value) => !value))
  ).pipe(shareReplay(1));

  private translations = this.adaptTranslateService.getCurrentLanguage();

  alertText = this.translations['adapt.agreement.navigation.alertText'];
  alertLinkText = this.translations['adapt.agreement.navigation.alertLinkText'];

  protected shellConfig$ = this.getShellConfig();

  private applicationSwitcherActionItems$ = combineLatest([
    this.applicationDescriptor$,
    this.rxGlobalCacheService.getBundleDescriptors(),
    this.shellConfig$,
    this.rxSystemConfigurationService.queryConfiguration(['Helix-Portal-URL']).pipe(
      map((value) => get(value, '[0].value')),
      catchError(() => of(null))
    )
  ]).pipe(
    take(1),
    switchMap(([currentBundleDescriptor, bundleDescriptors, shellConfig, helixPortalUrl]) => {
      if (shellConfig.allowAppSwitching) {
        const applicationList = _filter(bundleDescriptors, { isApplication: true, isLicensed: true });

        this.bundleDescriptors = bundleDescriptors;
        this.helixPortalUrl = helixPortalUrl as string;

        remove(applicationList, currentBundleDescriptor);
        remove(applicationList, { id: RX_APPLICATION.settingsBundleId });

        if (!this.rxCurrentUserService.isAdministrator()) {
          remove(applicationList, { id: RX_APPLICATION.dataloadBundleId });
        }

        if (!this.rxCurrentUserService.isAdministrator() && !this.rxCurrentUserService.isBusinessAnalyst()) {
          remove(applicationList, { id: RX_APPLICATION.innovationStudioBundleId });
        }

        remove(applicationList, { showInAppLauncher: false });

        const optedInApplications = applicationList.map((app) =>
          this.rxAngularApplicationService.isAngularJsApplication(app.id).pipe(
            map((isAngularJsApplication) => ({
              id: app.id,
              friendlyName: app.localizedDisplayName || app.friendlyName,
              isAngularJsApplication
            }))
          )
        );

        return forkJoin(optedInApplications);
      } else {
        return of([]);
      }
    }),
    map((applicationList) => {
      if (
        includes(['Fixed', 'Floating', 'Bundled'], this.rxCurrentUserService.get().licenseType) &&
        this.helixPortalUrl
      ) {
        applicationList.push({
          friendlyName: this.translateService.instant(
            'com.bmc.arsys.rx.client.shell.application-launcher.bmc-helix-dashboard.label'
          ),
          url: `${this.helixPortalUrl}/dashboards`
        });
      }

      if (applicationList.length) {
        return {
          name: this.translateService.instant('com.bmc.arsys.rx.client.shell.application-launcher.label'),
          tooltip: this.translateService.instant('com.bmc.arsys.rx.client.shell.application-launcher.label'),
          className: 'd-icon-tiles',
          switcher: {
            allItems: {
              items: sortBy(
                applicationList
                  .filter((app) => app.id !== this.rxGlobalCacheService.applicationId)
                  .map((app) => {
                    return {
                      name: app.friendlyName,
                      action: this.launchApplication.bind(this, app.id, app.isAngularJsApplication, app.url)
                    };
                  }),
                (application) => lowerCase(application.name)
              ) as any
            },
            recentItems: {} as any
          }
        };
      } else {
        return {};
      }
    })
  );

  private showUserMessagesAction: NavigationAction = {
    name: this.translateService.instant(RX_USER_MESSAGE.title),
    tooltip: this.translateService.instant(RX_USER_MESSAGE.title),
    count: 0,
    action: noop,
    className: 'd-icon-bell_o'
  };

  private administrationSettingsAction: NavigationAction = {
    name: this.translateService.instant('com.bmc.arsys.rx.client.common.settings.label'),
    tooltip: this.translateService.instant('com.bmc.arsys.rx.client.common.settings.label'),
    route: `/${this.rxGlobalCacheService.applicationId}/settings`,
    className: 'd-icon-gear'
  };

  navigationMenuItems$: Observable<IRxShellMenuItem[]>;

  navigationActionItems$: Observable<NavigationAction[]>;

  brandingInfo$ = this.applicationDescriptor$.pipe(
    map((applicationDescriptor) => ({
      logoClass: 'logo-helix',
      productName: applicationDescriptor.localizedDisplayName || applicationDescriptor.friendlyName,
      hideMobileLogo: false,
      switcher: {
        filter: {
          placeholder: this.translateService.instant('com.bmc.arsys.rx.client.common.search.label')
        }
      }
    }))
  );

  userProfileMenu$: Observable<ProfileModel>;

  protected constructor(protected injector: Injector) {
    this.navigationEnd$.pipe(skip(1), takeUntil(this.destroyed$)).subscribe({
      next: () => {
        this.highlightMenuItem(this.getActiveNavigationMenuItem());
      },
      complete: () => {
        this.rxUserMessageService.cancelMessagePolling();
      }
    });

    // ADAPT dropdowns listen to the click events and close themselves when the event is fired.
    // When user clicks inside an iframe though, the event is not propagated to the main window and the
    // dropdowns remain open.
    // Here we simulate the click on the document of the main window to close the dropdowns.
    this.removeWindowBlurListener = injector.get(Renderer2).listen('window', 'blur', () => {
      document.body.click();
    });
  }

  ngOnInit() {
    this.showUserMessagesAction.popover = this.userMessagesPopover;

    this.rxUserMessageService.messageCount$.subscribe((messageCount) => {
      this.showUserMessagesAction.count = messageCount;
    });

    this.navigationMenuItems$ = this.getNavigationMenuItems().pipe(
      tap(() => {
        // We perform a setTimeout according to Adapt documentation.
        // Else on smaller screens the menus might be displayed horizontally
        // in the shell rather than vertically for smaller screens.
        // https://github.bmc.com/pages/bmc-ux/adapt-angular/#/components/navigation
        setTimeout(() => {
          this.adaptNavigation.checkForHamburger();
          this.isShellInitialized = true;

          const activeNavigationMenuItem = this.getActiveNavigationMenuItem();

          this.highlightMenuItem(activeNavigationMenuItem);
        });
      })
    );

    this.navigationActionItems$ = combineLatest([
      this.shellConfig$,
      this.getNavigationActionItems(),
      this.applicationSwitcherActionItems$
    ]).pipe(
      map(([shellConfig, navigationActionItems, applicationSwitcherActionItems]) => [
        ...navigationActionItems,
        this.showUserMessagesAction,
        ...(shellConfig.administrationSettingsState ? [this.administrationSettingsAction] : []),
        applicationSwitcherActionItems
      ]),
      takeUntil(this.destroyed$)
    );

    const isGainsightFeatureEnabled = this.rxFeatureService.isFeatureEnabled('DRD21-11744');

    this.userProfileMenu$ = combineLatest([this.rxGainsightConfiguratorService.gainsightInitConfiguration$, this.getUserMenuItems()]).pipe(
      tap(([gainsightConfig, userMenuItems]) => {
        const analyticsMenuItem = find(userMenuItems, { id: 0 });

        if (analyticsMenuItem) {
          const shouldHideAnalyticsMenuItem =
            !isGainsightFeatureEnabled ||
            !gainsightConfig.settings.enableGainsight ||
            isEmpty(gainsightConfig.productTag);

          analyticsMenuItem.hide = shouldHideAnalyticsMenuItem;
        }
      }),
      map(([gainsightConfig, userMenuItems]) => {
        const currentUser = this.rxCurrentUserService.get();

        const userAvatarUrl = currentUser.personInstanceId
          ? this.rxRecordInstanceService.getAttachmentDownloadUrl(
              RX_USER.userProfileRecordDefinitionName,
              RX_USER.userProfilePictureFieldId,
              currentUser.personInstanceId
            )
          : '';

        return {
          desc: this.translateService.instant('com.bmc.arsys.rx.client.shell.user-profile.signedin.label'),
          userName: currentUser.fullName,
          image: userAvatarUrl,
          typeLong: true,
          inverted: false,
          signOut: {
            title: this.translateService.instant('com.bmc.arsys.rx.client.common.sign-out.label'),
            action: this.logout.bind(this)
          },
          menu: userMenuItems
        };
      })
    ) as Observable<any>;

    this.router.events.pipe(takeUntil(this.destroyed$)).subscribe((event) => {
      switch (true) {
        case event instanceof NavigationStart: {
          this.busySubscription = NEVER.subscribe();
          break;
        }

        case event instanceof NavigationEnd:
        case event instanceof NavigationCancel:
        case event instanceof NavigationError: {
          this.busySubscription?.unsubscribe();
          break;
        }

        default: {
          break;
        }
      }
    });
  }

  protected highlightMenuItem(menuItem: IRxShellMenuItem): void {
    if (menuItem) {
      this.currentMenuItemId = menuItem.id;
      this.adaptNavigation.selectMenuItem(this.currentMenuItemId);
    } else {
      const lastMenuItem = this.adaptNavigation.searchTree(this.adaptNavigation.menuModel, this.currentMenuItemId);

      if (lastMenuItem) {
        lastMenuItem.active = false;

        if (lastMenuItem.parent) {
          lastMenuItem.parent.active = false;
        }
      }

      this.currentMenuItemId = null;
      this.adaptNavigation.currentActiveId = null;
    }
  }

  protected logout(): void {
    let canDeactivate = this.rxComponentCanDeactivateGuard.canDeactivate();

    if (isBoolean(canDeactivate)) {
      canDeactivate = of(canDeactivate);
    }

    from(canDeactivate)
      .pipe(
        filter(Boolean),
        tap(() => {
          this.rxComponentCanDeactivateGuard.disable();
        }),
        switchMap(() => this.rxAuthService.logout())
      )
      .subscribe();
  }

  protected abstract getActiveNavigationMenuItem(): IRxShellMenuItem;

  protected abstract getNavigationActionItems(): Observable<NavigationAction[]>;

  protected abstract getNavigationMenuItems(): Observable<IRxShellMenuItem[]>;

  protected abstract getUserMenuItems(): Observable<IRxShellMenuItem[]>;

  protected abstract getShellConfig(): Observable<IRxShellConfig>;

  protected launchApplication(bundleId: string, isAngularJsApplication: boolean, portalUrl: string): void {
    let url;

    if (portalUrl) {
      url = portalUrl;
    } else {
      const application = find(this.bundleDescriptors, { id: bundleId });

      if (application.hasCustomEntryPoint && application.id !== RX_APPLICATION.innovationStudioBundleId) {
        url = `/${bundleId}/index.html`;
      } else if (isAngularJsApplication) {
        url = `/innovationsuite/index.html#/${bundleId}`;
      } else {
        url = `/helix/index.html#/${bundleId}`;
      }
    }

    window.open(url);
  }

  closeBanner(): void {
    this.closeBannerSubject.next(true);
  }

  ngOnDestroy(): void {
    this.removeWindowBlurListener();
    this.rxUserMessageService.cancelMessagePolling();
    this.closeBannerSubject.complete();

    this.destroyed$.next();
    this.destroyed$.complete();
  }
}
