import { Injectable } from '@angular/core';
import { OpenViewActionModalSize } from '@helix/platform/view/api';
import { Observable } from 'rxjs';
import { ExpressionEditorComponent } from './expression-editor.component';
import { IExpressionEditorConfig, IExpressionEditorPropertyChange } from './expression-editor.interfaces';
import { RxError } from '@helix/platform/utils';
import { isString } from 'lodash';
import { TranslateService } from '@ngx-translate/core';
import { IDialogApi, RxModalService } from '@helix/platform/ui-kit';

@Injectable({
  providedIn: 'root'
})
export class RxExpressionEditorService {
  constructor(private translateService: TranslateService, private rxModalService: RxModalService) {}

  openEditor(
    config: IExpressionEditorConfig,
    onDialogApiReady?: (dialogApi: IDialogApi) => void
  ): Observable<IExpressionEditorPropertyChange> {
    return new Observable((observer) => {
      this.rxModalService
        .openModal({
          title: this.translateService.instant('com.bmc.arsys.rx.client.expression-editor.edit-expression.title'),
          data: {
            ...config,
            api: {
              writeValue(propertyPath, propertyValue) {
                observer.next({ path: propertyPath, value: propertyValue });
              }
            },
            onApiReady: (dialogApi: IDialogApi) => {
              onDialogApiReady?.(dialogApi);
            }
          },
          content: ExpressionEditorComponent,
          size: (config.isReadOnly ? 'sm' : OpenViewActionModalSize.Large) as any
        })
        .then(() => observer.complete())
        .catch((e) => {
          onDialogApiReady?.(null);

          return isString(e) ? new RxError(e) : e;
        });
    });
  }
}
