import {
  Component,
  EventEmitter,
  HostBinding,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ChangeDetectorRef
} from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { concat, EMPTY, Observable, of, ReplaySubject, Subject } from 'rxjs';
import { distinctUntilChanged, finalize, switchMap, takeUntil, takeWhile, tap } from 'rxjs/operators';
import { flow, isArray, isEmpty, isNil, map, reduce, uniqueId } from 'lodash';
import {
  ExpressionParserToken,
  IDataDictionary,
  IDataDictionaryBranch,
  IFormControlComponent,
  RxDataDictionaryUtils,
  RxExpressionParserService
} from '@helix/platform/shared/api';
import { RxObjectUtilsService, RxTreeService } from '@helix/platform/utils';
import { IExpressionFormControlOptions } from './expression-form-control.types';
import { RX_EXPRESSION_EDITOR } from '../../expression-editor';
import { ValueAccessor } from '../../form-builder/value-accessor';

interface IExpressionNode {
  id: string;
  label?: string;
  expression: string;
  isInvalid?: boolean;
  tooltip?: string;
}

@Component({
  selector: 'rx-expression-form-control',
  templateUrl: 'expression-form-control.component.html',
  styleUrls: ['./expression-form-control.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: ExpressionFormControlComponent,
      multi: true
    }
  ]
})
export class ExpressionFormControlComponent
  extends ValueAccessor<string>
  implements OnInit, OnDestroy, IFormControlComponent
{
  @Input()
  options: IExpressionFormControlOptions;

  @Input()
  isDisabled: boolean;

  @HostBinding('attr.property-path')
  @Input()
  propertyPath: string;

  @HostBinding('attr.property-label')
  get propertyLabel(): string {
    return this.options?.expressionEditorPropertyName || this.options?.label || null;
  }

  @Output()
  events: EventEmitter<any> = new EventEmitter();

  nodes: Array<IExpressionNode> = [];

  private valueSubject = new Subject<string>();

  private dataDictionaryExpressionMap: Map<
    IDataDictionaryBranch['expression'],
    IDataDictionaryBranch & { tooltip: string }
  >;

  private expressionNodeMap = new Map<string, IExpressionNode>();
  private destroyed$ = new ReplaySubject<boolean>(1);
  private dataDictionary$: Observable<IDataDictionary>;
  isTouched = false;

  constructor(
    private rxExpressionParserService: RxExpressionParserService,
    private rxObjectUtilsService: RxObjectUtilsService,
    private rxDataDictionaryUtils: RxDataDictionaryUtils,
    private rxTreeService: RxTreeService,
    private changeDetectorRef: ChangeDetectorRef
  ) {
    super();
  }

  ngOnInit(): void {
    this.dataDictionary$ = this.options.dataDictionary$.pipe(takeUntil(this.destroyed$));

    (isNil(this.value) ? this.valueSubject.asObservable() : concat(of(this.value), this.valueSubject.asObservable()))
      .pipe(
        distinctUntilChanged(),
        switchMap((value) =>
          // When value is not defined don't subscribe to data dictionary observable.
          value
            ? this.dataDictionary$.pipe(
                tap(this.onDataDictionaryChange.bind(this)),
                // Unsubscribe from data dictionary observable when value is removed.
                takeWhile(() => Boolean(this.value))
              )
            : EMPTY.pipe(finalize(() => this.updateNodes()))
        ),
        takeUntil(this.destroyed$)
      )
      .subscribe();
  }

  onWriteValue(value: string): void {
    this.valueSubject.next(value);
  }

  openEditor(): void {
    this.isTouched = true;

    this.events.emit({
      type: RX_EXPRESSION_EDITOR.events.openExpressionEditor,
      payload: {
        propertyPath: this.propertyPath,
        propertyLabel: this.propertyLabel,
        isReadOnly: this.isDisabled
      }
    });
  }

  ngOnDestroy(): void {
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }

  private onDataDictionaryChange(dataDictionary: IDataDictionary) {
    this.dataDictionaryExpressionMap = flow(
      (dictionary) => this.rxDataDictionaryUtils.addTooltips(dictionary),
      (dictionary) =>
        this.rxTreeService.flatten({
          children: dictionary
        }),
      (dictionary) =>
        reduce(
          dictionary,
          (dictionary, node) => {
            if (isArray(node.expression)) {
              node.expression.forEach((expressionItem) => {
                dictionary.push({
                  ...node,
                  expression: expressionItem
                });
              });
            } else {
              dictionary.push(node);
            }

            return dictionary;
          },
          []
        ),
      (dictionary) => this.rxObjectUtilsService.mapFromArray(dictionary, 'expression')
    )(dataDictionary);

    this.updateNodes();
  }

  private getExpressionNode(token: string, expression: string): IExpressionNode {
    const node: IExpressionNode = {
      id: `$$rx-${uniqueId()}-rx$$`,
      expression
    };

    const isArExpression = token === ExpressionParserToken.ArExpression;

    if (
      token === ExpressionParserToken.RxExpression ||
      token === ExpressionParserToken.SingleQuoteRxExpression ||
      token === ExpressionParserToken.SingleQuoteTextExpression ||
      isArExpression
    ) {
      const dataDictionaryItem = this.dataDictionaryExpressionMap.get(expression);

      if (dataDictionaryItem) {
        node.label = dataDictionaryItem.label;
        node.tooltip = dataDictionaryItem.tooltip;
      } else if (isArExpression || token === ExpressionParserToken.SingleQuoteTextExpression) {
        node.label = expression;
      } else {
        node.isInvalid = true;
      }
    }

    return node;
  }

  private updateNodes(): void {
    if (this.dataDictionaryExpressionMap) {
      this.expressionNodeMap.clear();

      this.nodes = this.value
        ? map(
            this.rxExpressionParserService
              .parse(
                this.value,
                (token, expression) => {
                  if (token !== ExpressionParserToken.RxStringExpression) {
                    if (token === ExpressionParserToken.ArExpression) {
                      expression = expression.replace('\\', '');
                    }

                    const node = this.getExpressionNode(token, expression);

                    this.expressionNodeMap.set(node.id, node);

                    expression = node.id;
                  }

                  return expression;
                },
                this.options.operators
              )
              .split(/(\$\$rx-[\d]+-rx\$\$)/g)
              .filter((element) => !isEmpty(element) && !/^((\$\$rx-)|(-rx\$\$)|([\s]+))$/.test(element)),
            (nodeId) =>
              this.expressionNodeMap.get(nodeId) || {
                id: nodeId,
                expression: nodeId
              }
          )
        : [];

      this.changeDetectorRef.markForCheck();
    }
  }
}
