import { Injectable } from '@angular/core';
import { RxWizardModalComponent } from './wizard-modal.component';
import { IWizardConfig } from './wizard.types';
import { RxModalService } from '@helix/platform/ui-kit';
import { noop } from 'lodash';

@Injectable({
  providedIn: 'root'
})
export class RxWizardService {
  constructor(private rxModalService: RxModalService) {}

  open<TContext>(config: IWizardConfig<TContext>): Promise<TContext> {
    return this.rxModalService
      .openModal({
        content: RxWizardModalComponent,
        data: {
          ...config
        }
      })
      .catch(noop);
  }
}
