import { Directive, EventEmitter, Injector, Input, Output } from '@angular/core';
import { IInspector } from './inspector.interface';
import { InspectorDirective } from './inspector.directive';
import { ValueAccessor } from '../form-builder';
import { IFormControlComponent } from '@helix/platform/shared/api';

@Directive()
// tslint:disable-next-line:directive-class-suffix
export class InspectorControlBase<TInspectorControlValue, TInspectorControlOptions, TDesignerItemModel>
  extends ValueAccessor<TInspectorControlValue>
  implements IFormControlComponent
{
  @Input()
  propertyPath: string;

  @Input()
  options: TInspectorControlOptions;

  @Output()
  events: EventEmitter<any> = new EventEmitter();

  readonly designerItemModel = this.injector.get<IInspector<TDesignerItemModel>>(InspectorDirective).designerItemModel;

  constructor(protected injector: Injector) {
    super();
  }
}
