import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, NG_VALUE_ACCESSOR } from '@angular/forms';
import { ReplaySubject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { ValueAccessor } from '../../form-builder';
import { IInputListFormControlOptions } from './input-list-form-control-options.interface';

@Component({
  selector: 'rx-input-list-form-control',
  templateUrl: './input-list-form-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: InputListFormControlComponent,
      multi: true
    }
  ]
})
export class InputListFormControlComponent extends ValueAccessor<string[]> implements OnInit, OnDestroy {
  @Input()
  options: IInputListFormControlOptions;

  formArray: FormArray = this.formBuilder.array([]);

  private destroyed$ = new ReplaySubject<void>(1);

  constructor(private formBuilder: FormBuilder) {
    super();
  }

  ngOnInit(): void {
    this.formArray.valueChanges.pipe(takeUntil(this.destroyed$)).subscribe(() => {
      if (this.formArray.controls.length) {
        this.value = this.formArray.getRawValue();
      } else {
        this.value = null;
      }
    });
  }

  addItem(): void {
    this.formArray.push(new FormControl(''));
  }

  removeItem(index: number) {
    this.formArray.removeAt(index);
  }

  ngOnDestroy() {
    this.destroyed$.next();
    this.destroyed$.complete();
  }
}
