import { ChangeDetectionStrategy, Component, OnDestroy } from '@angular/core';
import { ActiveModalRef, AdaptAgreementState } from '@bmc-ux/adapt-angular';
import { RxGainsightUserPreferencesService } from '../gainsight-user-preferences.service';
import { catchError, filter, map, shareReplay, switchMap, take, tap } from 'rxjs/operators';
import { IGainsightUserPreferences } from '../gainsight.types';
import { RxNotificationService } from '@helix/platform/shared/api';
import { TranslateService } from '@ngx-translate/core';
import { BehaviorSubject, combineLatest, of, throwError } from 'rxjs';

@Component({
  selector: 'rx-gainsight-opt-in',
  templateUrl: './gainsight-opt-in.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class GainsightOptInComponent implements OnDestroy {
  isQueryInProgress = true;
  isSaveButtonDisabled = true;
  private accountPerformance: boolean;

  private isSaveInProgressSubject = new BehaviorSubject<boolean>(false);

  private gainsightUserPreferences$ = this.rxGainsightUserPreferencesService.getGainsightUserPreferences().pipe(
    take(1),
    catchError(() => {
      this.cancel();

      this.rxNotificationService.addWarningMessage(
        this.translateService.instant('com.bmc.arsys.rx.client.gainsight.user-profile-not-created.message')
      );

      return of(null);
    }),
    shareReplay(1)
  );

  state$ = this.gainsightUserPreferences$.pipe(
    tap(() => {
      this.isQueryInProgress = false;
    }),
    filter((gainsightUserPreferences) => Boolean(gainsightUserPreferences)),
    map((gainsightUserPreferences: IGainsightUserPreferences) => ({
      accountPerformance: gainsightUserPreferences.trackUsage != false,
      accountMarketing: false
    }))
  );

  vm$ = combineLatest([this.state$, this.isSaveInProgressSubject]).pipe(
    map(([state, isSaveInProgress]) => ({
      state,
      isSaveInProgress
    }))
  );

  constructor(
    private activeModalRef: ActiveModalRef,
    private rxGainsightUserPreferencesService: RxGainsightUserPreferencesService,
    private translateService: TranslateService,
    private rxNotificationService: RxNotificationService
  ) {}

  handleStateChange(state: AdaptAgreementState): void {
    this.isSaveButtonDisabled = false;
    this.accountPerformance = state.accountPerformance;
  }

  cancel(): void {
    this.activeModalRef.dismiss();
  }

  save(): void {
    this.isSaveInProgressSubject.next(true);

    this.gainsightUserPreferences$
      .pipe(
        take(1),
        map((gainsightUserPreferences: IGainsightUserPreferences) => ({
          ...gainsightUserPreferences,
          trackUsage: this.accountPerformance
        })),
        switchMap((gainsightUserPreferences: IGainsightUserPreferences) =>
          this.rxGainsightUserPreferencesService.saveGainsightUserPreferences(gainsightUserPreferences)
        ),
        catchError((error) => {
          this.isSaveInProgressSubject.next(false);

          return throwError(error);
        })
      )
      .subscribe(
        () => {
          this.rxNotificationService.addSuccessMessage(
            this.translateService.instant('com.bmc.arsys.rx.client.gainsight.user-preferences-saved.success.message')
          );

          this.isSaveInProgressSubject.next(false);
          this.activeModalRef.close();
        },
        () =>
          this.rxNotificationService.addErrorMessage(
            this.translateService.instant('com.bmc.arsys.rx.client.gainsight.user-profile-not-updated.message')
          )
      );
  }

  ngOnDestroy(): void {
    this.isSaveInProgressSubject.complete();
  }
}
