import { Injectable } from '@angular/core';
import {
  AdaptRadarGainsightIdentifyActionName,
  AdaptRadarGainsightProvider,
  AdaptRadarProductAreaName,
  AdaptRadarService,
  AdaptRadarSupportedProviders
} from '@bmc-ux/adapt-radar';
import { map, switchMap, take, tap, shareReplay } from 'rxjs/operators';
import {
  IBundleDescriptor,
  IPlainObject,
  IUser,
  RX_APPLICATION,
  RxCurrentUserService,
  RxFeatureService,
  RxGlobalCacheService,
  RxLogService
} from '@helix/platform/shared/api';
import { RxRecordInstanceService, RecordInstance, RxRecordInstanceUpdateService } from '@helix/platform/record/api';
import { RX_GAINSIGHT } from './gainsight.constant';
import { IGainsightConfiguration, IGainsightSettings } from './gainsight.types';
import { Observable, of, combineLatest, iif } from 'rxjs';
import { find, keys, merge } from 'lodash';
import { HttpClient } from '@angular/common/http';
import { RxIframeUtilsService } from '@helix/platform/utils';

declare function aptrinsic(gainsightEventName: string, customSettings: any, customEventData: any): void;

@Injectable({
  providedIn: 'root'
})
export class RxGainsightConfiguratorService {
  private productTag: string;
  private updatedContext: IPlainObject;
  private isGainsightLoaded: boolean;
  private useAdaptRadar: boolean;
  private bundleDescriptor$ = this.rxGlobalCacheService.getApplicationBundleDescriptor().pipe(take(1), shareReplay(1));

  private gainsightConfiguration$ = this.bundleDescriptor$.pipe(
    switchMap((bundleDescriptor: IBundleDescriptor) => this.getGainsightConfiguration(bundleDescriptor.id)),
    shareReplay(1)
  );

  private globalContextData$ = combineLatest([this.bundleDescriptor$, this.gainsightConfiguration$]).pipe(
    map(([bundleDescriptor, gainsightConfiguration]) =>
      bundleDescriptor.id === RX_APPLICATION.innovationStudioBundleId
        ? {
            productArea: {
              name: AdaptRadarProductAreaName.Platform,
              version: bundleDescriptor.displayVersion,
              hosting: gainsightConfiguration.settings.deploymentType
            }
          }
        : null
    ),
    shareReplay(1)
  );

  constructor(
    private rxGlobalCacheService: RxGlobalCacheService,
    private rxRecordInstanceService: RxRecordInstanceService,
    private adaptRadarService: AdaptRadarService,
    private adaptRadarGainsightProvider: AdaptRadarGainsightProvider,
    private rxCurrentUserService: RxCurrentUserService,
    private rxFeatureService: RxFeatureService,
    private rxLogService: RxLogService,
    private httpClient: HttpClient,
    private rxRecordInstanceUpdateService: RxRecordInstanceUpdateService,
    private rxIframeUtilsService: RxIframeUtilsService
  ) {}

  gainsightInitConfiguration$ = iif(
    () => this.rxFeatureService.isFeatureEnabled('DRD21-11744'),
    combineLatest([this.bundleDescriptor$, this.gainsightConfiguration$]).pipe(
      tap(([bundleDescriptor, gainsightConfiguration]) => {
        const company = gainsightConfiguration.company;
        const companyId = gainsightConfiguration.companyId;
        const user = this.rxCurrentUserService.get();
        const hashedUserId = gainsightConfiguration.hashedUserId;
        let isGainsightEnabled = gainsightConfiguration.settings.enableGainsight;
        this.useAdaptRadar = gainsightConfiguration.settings.useAdaptRadar;
        this.productTag = gainsightConfiguration.productTag;

        if (isGainsightEnabled && !gainsightConfiguration.productTag) {
          this.rxLogService.debug(`Gainsight product tag is missing for bundle ${bundleDescriptor.id}.`);
          isGainsightEnabled = false;
        }

        if (isGainsightEnabled) {
          if (this.useAdaptRadar) {
            this.adaptRadarService.startDataCollecting({
              providers: [
                {
                  name: AdaptRadarSupportedProviders.Gainsight,
                  id: this.productTag,
                  oneTimeActions: [
                    {
                      name: AdaptRadarGainsightIdentifyActionName.UserIdentify,
                      data: {
                        userData: {
                          id: hashedUserId,
                          role: user.isAdministrator
                            ? RX_GAINSIGHT.administratorRole
                            : user.isBusinessAnalyst
                            ? RX_GAINSIGHT.businessAnalystRole
                            : RX_GAINSIGHT.regularUserRole
                        },
                        accountData: {
                          id: companyId || company,
                          name: company
                        }
                      }
                    }
                  ]
                }
              ]
            });
          } else {
            const gainsightUrl = gainsightConfiguration.settings.loadGainsightFromBmcIt
              ? RX_GAINSIGHT.bmcGainsightUrl
              : RX_GAINSIGHT.gainsightUrl;

            this.loadGainsightScript(user, gainsightUrl, hashedUserId, company, companyId);
          }
        }
      }),
      switchMap(([bundleDescriptor, gainsightConfiguration]) =>
        gainsightConfiguration.settings.enableGainsight && gainsightConfiguration.settings.useAdaptRadar
          ? this.adaptRadarGainsightProvider.init$
          : of(false)
      ),
      tap((isInitialized) => {
        if (isInitialized) {
          this.isGainsightLoaded = true;
          this.setGlobalContext(this.updatedContext);
        }
      }),
      switchMap(() => this.gainsightConfiguration$),
      shareReplay(1)
    ),
    of(null)
  );

  updateGlobalContext(globalContext: any, viewDefinitionName?: string): void {
    this.gainsightConfiguration$.pipe(take(1)).subscribe((gainsightConfiguration) => {
      if (this.rxFeatureService.isFeatureEnabled('DRD21-11744')) {
        if (viewDefinitionName) {
          // For runtime views clear all the previous global context before update
          this.removeGlobalContext(keys(this.updatedContext));

          globalContext =
            find(gainsightConfiguration.viewMapping, { viewName: viewDefinitionName })?.globalContext ??
            find(gainsightConfiguration.viewMapping, { default: true })?.globalContext;

          globalContext = globalContext && JSON.parse(globalContext);

          this.updatedContext = globalContext;
        }

        this.setGlobalContext(globalContext);
      }
    });
  }

  private setGlobalContext(globalContext: IPlainObject): void {
    this.globalContextData$.pipe(take(1)).subscribe((globalContextData) => {
      if (this.useAdaptRadar && this.isGainsightLoaded) {
        this.adaptRadarGainsightProvider.setGlobalContext(merge(globalContextData, globalContext));
      } else if (!this.useAdaptRadar && this.isGainsightLoaded) {
        aptrinsic('set', 'globalContext', merge(globalContextData, globalContext));
      } else {
        this.updatedContext = globalContext;
      }
    });
  }

  removeGlobalContext(contextList: string[]): void {
    if (this.isGainsightLoaded) {
      // Clear all global context if list is not specified
      contextList ??= keys(this.updatedContext);

      this.globalContextData$.pipe(take(1)).subscribe((globalContextData) => {
        if (globalContextData) {
          contextList.forEach((context) => delete globalContextData[context]);
        }

        if (this.useAdaptRadar) {
          this.adaptRadarGainsightProvider.removeGlobalContext(contextList);
        } else {
          aptrinsic('remove', 'globalContext', contextList);
        }
      });
    }
  }

  private async loadGainsightScript(
    user: IUser,
    gainsightUrl: string,
    hashedUserId: string,
    company: string,
    companyId: string
  ): Promise<void> {
    const param = this.productTag;
    const script = document.getElementsByTagName('script')[0];
    const node = document.createElement('script');

    window['aptrinsic'] =
      window['aptrinsic'] ||
      function () {
        (window['aptrinsic'].q = window['aptrinsic'].q || []).push(arguments);
      };

    window['aptrinsic'].p = param;

    // iframeModeEnabled is set to "false" when embedded in an iFrame, as per Gainsight documentation.
    window['aptrinsic'].c = { iframeModeEnabled: !this.rxIframeUtilsService.isRunningInIframe() };

    node.async = true;
    node.src = gainsightUrl + '?a=' + param;

    node.onload = () => {
      const functionalRoles = user.functionalRoles.reduce((functionalRole, name) => {
        functionalRole[name] = true;
        return functionalRole;
      }, {});

      this.isGainsightLoaded = true;

      aptrinsic(
        'identify',
        {
          id: hashedUserId,
          globalId: hashedUserId,
          role: user.isAdministrator
            ? RX_GAINSIGHT.administratorRole
            : user.isBusinessAnalyst
            ? RX_GAINSIGHT.businessAnalystRole
            : RX_GAINSIGHT.regularUserRole,
          functionalRoles
        },
        {
          id: companyId || company,
          name: company
        }
      );

      this.setGlobalContext(this.updatedContext);
    };

    node.onerror = (error: any) => {
      this.rxLogService.error(error);
    };

    script.parentNode.insertBefore(node, script);
  }

  getGainsightConfiguration(bundleId: string): Observable<IGainsightConfiguration> {
    if (this.rxFeatureService.isFeatureEnabled('DRD21-11744')) {
      return this.httpClient.get<IGainsightConfiguration>(RX_GAINSIGHT.gainsightConfigurationsApi + '/' + bundleId);
    } else {
      return of(null);
    }
  }

  saveGainsightConfiguration(gainsightSettings: IGainsightSettings): Observable<any> {
    return this.rxRecordInstanceService
      .get(RX_GAINSIGHT.gainsightSettings.recordDefinitionName, RX_GAINSIGHT.gainsightSettings.recordInstanceId)
      .pipe(
        switchMap((recordInstance: RecordInstance) => {
          recordInstance.setFieldValue(
            RX_GAINSIGHT.gainsightSettings.fieldIds.enableGainsight,
            gainsightSettings.enableGainsight
          );

          recordInstance.setFieldValue(
            RX_GAINSIGHT.gainsightSettings.fieldIds.useAdaptRadar,
            gainsightSettings.useAdaptRadar
          );

          recordInstance.setFieldValue(
            RX_GAINSIGHT.gainsightSettings.fieldIds.loadGainsightFromBmcIt,
            gainsightSettings.loadGainsightFromBmcIt
          );

          recordInstance.setFieldValue(
            RX_GAINSIGHT.gainsightSettings.fieldIds.deploymentType,
            gainsightSettings.deploymentType
          );

          recordInstance.setFieldValue(
            RX_GAINSIGHT.gainsightSettings.fieldIds.environmentType,
            gainsightSettings.environmentType
          );

          return this.rxRecordInstanceUpdateService.execute(recordInstance);
        })
      );
  }
}
