import { Component, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { cloneDeep } from 'lodash';
import { ValueAccessor } from '../form-builder/value-accessor';
import { IPermissionEditorComponentOptions } from './interfaces/permission-editor-component-options.interface';
import { PermissionOwner } from './interfaces/permission.interfaces';
import { RxPermissionEditorDialogComponent } from './permission-editor-dialog/permission-editor-dialog.component';
import { IDialogApi, RxModalService } from '@helix/platform/ui-kit';

@Component({
  selector: 'rx-permission-editor',
  templateUrl: './permission-editor.component.html',
  styleUrls: ['./permission-editor.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: RxPermissionEditorComponent,
      multi: true
    }
  ]
})
export class RxPermissionEditorComponent extends ValueAccessor<PermissionOwner[]> {
  private dialogApi: IDialogApi;

  @Input()
  options: IPermissionEditorComponentOptions;

  constructor(private rxModalService: RxModalService) {
    super();
  }

  openEditor(): void {
    this.rxModalService
      .openModal({
        content: RxPermissionEditorDialogComponent,
        data: {
          assignedPermissions: cloneDeep(this.value),
          type: this.options.type,
          permissionScope: this.options.permissionScope,
          onApiReady: (dialogApi: IDialogApi) => {
            this.dialogApi = dialogApi;
          }
        }
      })
      .then((data: { permissions: PermissionOwner[] }) => {
        this.dialogApi = null;
        this.value = data.permissions;
      })
      .catch(() => {
        this.dialogApi = null;
      });
  }

  isDirty(): boolean {
    return Boolean(this.dialogApi?.isDirty());
  }
}
