import { Component, EventEmitter, Input, Output } from '@angular/core';
import { IBreadcrumbItem } from '@helix/platform/ui-kit';

@Component({
  selector: 'rx-designer-header',
  templateUrl: './designer-header.component.html',
  styleUrls: ['./designer-header.component.scss']
})
export class RxDesignerHeaderComponent {
  @Input()
  bundleName: string;

  @Input()
  breadcrumbItems: IBreadcrumbItem[] = [];

  @Input()
  isDesignMode = true;

  @Input()
  isPreviewAvailable = false;

  @Input()
  isSaveButtonDisabled = false;

  @Output()
  breadcrumbSelected: EventEmitter<IBreadcrumbItem> = new EventEmitter<IBreadcrumbItem>();

  @Output()
  toggleDesignMode: EventEmitter<void> = new EventEmitter();

  @Output()
  showPreview: EventEmitter<void> = new EventEmitter();

  @Output()
  save: EventEmitter<void> = new EventEmitter();

  @Output()
  closeDesigner: EventEmitter<void> = new EventEmitter<void>();
}
