import { RxStringService } from '@helix/platform/utils';
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, NG_VALUE_ACCESSOR } from '@angular/forms';
import { IFormControlComponent } from '@helix/platform/shared/api';
import { ValueAccessor } from '../../form-builder/value-accessor';
import { IListFormControlOptions } from './list-form-control-options.interface';
import { forEach, isEqual } from 'lodash';
import { ReplaySubject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { IPlainObject } from '@helix/platform/shared/api';

@Component({
  selector: 'rx-list-form-control',
  templateUrl: './list-form-control.component.html',
  styleUrls: ['./list-form-control.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: ListFormControlComponent,
      multi: true
    }
  ]
})
export class ListFormControlComponent extends ValueAccessor<any[]> implements IFormControlComponent, OnInit, OnDestroy {
  @Input()
  options: IListFormControlOptions;

  @Input()
  propertyPath: string;

  @Output()
  events = new EventEmitter<any>();

  itemList: FormArray = this.formBuilder.array([]);

  private destroyed$ = new ReplaySubject<boolean>(1);

  constructor(private formBuilder: FormBuilder, public stringService: RxStringService) {
    super();
  }

  ngOnInit(): void {
    this.itemList.valueChanges.pipe(takeUntil(this.destroyed$)).subscribe((value) => {
      this.value = value;
    });
  }

  setDisabledState(isDisabled: boolean): void {
    super.setDisabledState(isDisabled);

    if (isDisabled) {
      this.itemList.disable();
    } else {
      this.itemList.enable();
    }
  }

  ngOnDestroy(): void {
    this.destroyed$.next(true);
    this.destroyed$.complete();
    this.clearItems();
  }

  onWriteValue(modelValue): void {
    if (!isEqual(modelValue, this.itemList.value)) {
      this.clearItems();

      // initialize values
      forEach(modelValue, (item) => this.addItem(item));
    }
  }

  clearItems(): void {
    this.itemList.clear();
  }

  addItem(item: IPlainObject): void {
    const itemControl = this.createItemFormGroup(item);

    this.itemList.push(itemControl);
  }

  createItemFormGroup(item: IPlainObject): FormGroup {
    const formGroupData: IPlainObject = {};

    this.options.items.forEach((i) => {
      formGroupData[i.propertyName] = item[i.propertyName] || null;
    });

    return this.formBuilder.group(formGroupData);
  }

  onItemRemove(index: number): void {
    this.itemList.removeAt(index);
  }

  onAddItem(): void {
    this.addItem({});
  }
}
