import {
  Component,
  ElementRef,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  QueryList,
  Renderer2,
  SimpleChanges,
  ViewChildren
} from '@angular/core';
import { FormArray, FormBuilder, FormControl, NG_VALUE_ACCESSOR } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { ReplaySubject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { isEqual } from 'lodash';

import { RX_MODAL, RxModalService } from '@helix/platform/ui-kit';
import { IFormBuilderEvent, IFormControlComponent, IFormFocusable, IPlainObject } from '@helix/platform/shared/api';

import { ValueAccessor } from '../../form-builder/value-accessor';
import { IExpressionEditorPropertyChange } from '../../expression-editor/expression-editor.interfaces';
import { RxExpressionEditorService } from '../../expression-editor/expression-editor.service';
import {
  AssignmentExpressionListTargetFieldType,
  IAssignmentExpressionListFormControlOptions
} from './assignment-expression-list-form-control.types';

@Component({
  selector: 'rx-assignment-expression-list-form-control',
  templateUrl: './assignment-expression-list-form-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: AssignmentExpressionListFormControlComponent,
      multi: true
    }
  ]
})
export class AssignmentExpressionListFormControlComponent
  extends ValueAccessor<any[]>
  implements IFormControlComponent, IFormFocusable, OnInit, OnChanges, OnDestroy
{
  @Input()
  options: IAssignmentExpressionListFormControlOptions;

  @Input()
  propertyPath: string;

  @ViewChildren('fieldGroups', { read: ElementRef })
  private fieldGroups: QueryList<ElementRef>;

  currentOptions: IAssignmentExpressionListFormControlOptions;
  formArray: FormArray = this.formBuilder.array([]);

  private defaultOptions = {
    addItemText: 'com.bmc.arsys.rx.client.common.add.label',
    confirmationMessage: 'com.bmc.arsys.rx.client.common.delete-item-confirmation.message',
    sourceFieldOptions: {
      options: {
        label: 'com.bmc.arsys.rx.client.designer.assignment-expression.source.label',
        required: true
      },
      propertyName: 'expression'
    },
    targetFieldOptions: {
      options: {
        label: 'com.bmc.arsys.rx.client.designer.assignment-expression.target.label',
        required: true
      },
      propertyName: 'assignTarget'
    }
  };

  private destroyed$ = new ReplaySubject<boolean>(1);

  constructor(
    private formBuilder: FormBuilder,
    private renderer: Renderer2,
    private rxExpressionEditorService: RxExpressionEditorService,
    private rxModalService: RxModalService,
    private translateService: TranslateService
  ) {
    super();
  }

  ngOnInit(): void {
    this.formArray.valueChanges.pipe(takeUntil(this.destroyed$)).subscribe((value) => {
      this.value = value;
    });

    this.patchOptions(this.options);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.options.currentValue !== changes.options.previousValue) {
      this.patchOptions(changes.options.currentValue);
    }
  }

  ngOnDestroy(): void {
    this.formArray.clear();

    this.destroyed$.next(true);
    this.destroyed$.complete();
  }

  focus(data: IPlainObject): void {
    const fieldGroup = this.fieldGroups.get(data.index);

    if (data.fieldName === this.currentOptions.targetFieldOptions.propertyName) {
      const targetFieldElement =
        this.currentOptions.targetFieldOptions.type === AssignmentExpressionListTargetFieldType.Text
          ? fieldGroup.nativeElement.querySelector('rx-text-form-control input')
          : fieldGroup.nativeElement.querySelector('rx-select-form-control button');

      targetFieldElement.focus();
    } else if (data.fieldName === this.currentOptions.sourceFieldOptions.propertyName) {
      const sourceFieldButton = fieldGroup.nativeElement.querySelector('rx-expression-form-control button');

      sourceFieldButton.focus();
      sourceFieldButton.click();
    }
  }

  onWriteValue(value: IPlainObject[]): void {
    if (!isEqual(value, this.formArray.value)) {
      this.formArray.clear();

      value.forEach((item) => this.addItem(item));
    }
  }

  setDisabledState(isDisabled: boolean): void {
    super.setDisabledState(isDisabled);

    if (isDisabled) {
      this.formArray.disable();
    } else {
      this.formArray.enable();
    }
  }

  addItem(item?: IPlainObject): void {
    this.formArray.push(
      this.formBuilder.group(
        item ?? {
          [this.currentOptions.targetFieldOptions.propertyName]: null,
          [this.currentOptions.sourceFieldOptions.propertyName]: null
        }
      )
    );
  }

  openExpressionEditor(event: IFormBuilderEvent, formControl: FormControl): void {
    this.rxExpressionEditorService
      .openEditor({
        expressionConfigurator: this.currentOptions.sourceFieldOptions.expressionConfigurator,
        isReadOnly: this.isDisabled,
        property: {
          path: event.payload.propertyPath,
          value: formControl.value,
          label: event.payload.propertyLabel
        }
      })
      .subscribe((expression: IExpressionEditorPropertyChange) => {
        formControl.setValue(expression.value);
      });
  }

  removeItem(index: number): void {
    if (!this.isDisabled) {
      this.rxModalService
        .confirm({
          title: this.translateService.instant('com.bmc.arsys.rx.client.common.warning.label'),
          modalStyle: RX_MODAL.modalStyles.warning,
          message: this.currentOptions.confirmationMessage
        })
        .then((confirmed) => {
          if (confirmed) {
            this.formArray.removeAt(index);
          }
        });
    }
  }

  private patchOptions(options: IAssignmentExpressionListFormControlOptions): void {
    this.currentOptions = {
      addItemText: this.translateService.instant(options.addItemText || this.defaultOptions.addItemText),
      confirmationMessage: this.translateService.instant(
        options.confirmationMessage || this.defaultOptions.confirmationMessage
      ),
      sourceFieldOptions: {
        expressionConfigurator: options.sourceFieldOptions.expressionConfigurator,
        options: {
          label: this.translateService.instant(this.defaultOptions.sourceFieldOptions.options.label),
          isRequired: this.defaultOptions.sourceFieldOptions.options.required,
          ...options.sourceFieldOptions.options
        },
        propertyName: options.sourceFieldOptions.propertyName || this.defaultOptions.sourceFieldOptions.propertyName
      },
      targetFieldOptions: {
        options: {
          label: this.translateService.instant(this.defaultOptions.targetFieldOptions.options.label),
          required: this.defaultOptions.targetFieldOptions.options.required,
          ...options.targetFieldOptions.options
        },
        propertyName: options.targetFieldOptions.propertyName || this.defaultOptions.targetFieldOptions.propertyName,
        type: options.targetFieldOptions.type
      }
    };
  }
}
