import { Component, Injector } from '@angular/core';
import { ActiveModalRef, DismissReasons, RxSelectionChangeEvent, RxSelectOption } from '@bmc-ux/adapt-angular';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { RX_MODAL, RxModalClass, RxModalService } from '@helix/platform/ui-kit';
import { find, noop } from 'lodash';
import {
  ICustomizationOptionsEditorData,
  IScopeSelectionOption,
  RX_BUNDLE,
  RX_OVERLAY
} from '@helix/platform/shared/api';

@Component({
  selector: 'rx-scope-customization-modal',
  templateUrl: './coarse-grained-customization-options-editor.component.html'
})
export class CoarseGrainedCustomizationOptionsEditorComponent extends RxModalClass {
  data: ICustomizationOptionsEditorData = this.activeModalRef.getData();
  scopeSelectionOptions: IScopeSelectionOption[] = this.data.scopeSelectionOptions;
  customizationOptionsForm: FormGroup;
  isDisabled = this.data.overlayOperation !== RX_OVERLAY.operationTypes.createdInThisOverlayGroup;

  constructor(
    private formBuilder: FormBuilder,
    private rxModalService: RxModalService,
    public activeModalRef: ActiveModalRef,
    protected injector: Injector
  ) {
    super(activeModalRef, injector);
    this.initForm();
  }

  isDirty(): boolean {
    return this.customizationOptionsForm.dirty;
  }

  isPublic(): boolean {
    return this.customizationOptionsForm.get('scope').value[0].id === RX_BUNDLE.definitionScopeTypes.public;
  }

  optionFormatter(option: RxSelectOption): string {
    return option.name;
  }

  submit(): void {
    if (this.isPublic()) {
      this.rxModalService
        .confirm({
          title: 'Warning',
          modalStyle: RX_MODAL.modalStyles.warning,
          message:
            'If the definition scope is set to Public, it cannot be changed once the definition gets saved. Do you want to continue?'
        })
        .then((result: boolean) => {
          if (result) {
            this.closeModal();
          }
        })
        .catch(noop);
    } else if (this.data.definitionScopeName === RX_BUNDLE.definitionScopeNames.public) {
      this.rxModalService
        .confirm({
          title: 'Warning',
          modalStyle: RX_MODAL.modalStyles.warning,
          message:
            'Changing the definition scope from Public to Application or Library can break upgrades. Do you want to continue?'
        })
        .then((result: boolean) => {
          if (result) {
            this.closeModal();
          }
        })
        .catch(noop);
    } else {
      this.closeModal();
    }
  }

  onScopeChange(rxSelectionChangeEvent: RxSelectionChangeEvent): void {
    if (rxSelectionChangeEvent.options[0].id === RX_BUNDLE.definitionScopes.application.type) {
      this.customizationOptionsForm.get('allowOverlay').setValue(false);
    }
  }

  closeModal(): void {
    const result = this.customizationOptionsForm.getRawValue();
    result.scope = result.scope[0].id;
    this.activeModalRef.close(result);
  }

  private initForm(): void {
    const definitionScopeName = find(
      this.scopeSelectionOptions,
      (scopeType) => scopeType.name === this.data.definitionScopeName
    );

    this.customizationOptionsForm = this.formBuilder.group({
      scope: new FormControl([definitionScopeName]),
      allowOverlay: { value: this.data.allowOverlay || false, disabled: this.data.isDisabled }
    });
  }

  cancel(): void {
    this.activeModalRef.dismiss(DismissReasons.CLOSE_BTN);
  }
}
