import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { IGainsightUserPreferences } from './gainsight.types';
import { RxFeatureService } from '@helix/platform/shared/api';
import { RX_GAINSIGHT } from './gainsight.constant';

@Injectable({
  providedIn: 'root'
})
export class RxGainsightUserPreferencesService {
  constructor(private httpClient: HttpClient, private rxFeatureService: RxFeatureService) {}

  getGainsightUserPreferences(): Observable<IGainsightUserPreferences> {
    if (this.rxFeatureService.isFeatureEnabled('DRD21-11744')) {
      return this.httpClient.get<IGainsightUserPreferences>(RX_GAINSIGHT.gainsightUserPreferencesApi);
    } else {
      return of(null);
    }
  }

  saveGainsightUserPreferences(gainsightUserPreferences: IGainsightUserPreferences): Observable<any> {
    return this.httpClient.put(RX_GAINSIGHT.gainsightUserPreferencesApi, gainsightUserPreferences);
  }
}
