import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';
import { forEach } from 'lodash';

import { ICreatedRecordInstance, RecordInstance, RxRecordInstanceService } from '@helix/platform/record/api';
import {
  IBundleDescriptor,
  IServerResponseMessage,
  RxBundleCacheService,
  RxGlobalCacheService
} from '@helix/platform/shared/api';
import { RX_ISSUE_REPORTER } from './issue-reporter.constant';
import { IRxIssueDetails } from './issue-details.interface';

@Injectable()
export class RxIssueReporterService {
  constructor(
    private rxRecordInstanceService: RxRecordInstanceService,
    private rxGlobalCacheService: RxGlobalCacheService,
    private rxBundleCacheService: RxBundleCacheService
  ) {}

  reportIssue(message: string, data: IServerResponseMessage): Observable<ICreatedRecordInstance> {
    return this.prepareIssueDetails(message, data).pipe(
      switchMap((issueDetails) => {
        return this.rxRecordInstanceService.getNew(RX_ISSUE_REPORTER.recordDefinitionName).pipe(
          map((recordInstance: RecordInstance) => {
            forEach(RX_ISSUE_REPORTER.recordFields, (field) => {
              let fieldValue = issueDetails[field.name] || null;

              if (field.id === RX_ISSUE_REPORTER.recordFields.messageType.id) {
                fieldValue = RX_ISSUE_REPORTER.messageTypeOptions[fieldValue];
              }

              recordInstance.setFieldValue(field.id, fieldValue);
            });

            return recordInstance;
          })
        );
      }),
      switchMap((recordInstance) => this.rxRecordInstanceService.create(recordInstance))
    );
  }

  private prepareIssueDetails(message: string, error: IServerResponseMessage = {}): Observable<IRxIssueDetails> {
    return this.rxGlobalCacheService.getBundleDescriptor(this.rxBundleCacheService.bundleId).pipe(
      map((bundleDescriptor: IBundleDescriptor) => ({
        details: message,
        applicationName: bundleDescriptor.friendlyName || null,
        messageType: error.messageType || null,
        messageNumber: error.messageNumber || null,
        operationId: error.operationId || null,
        messageText: error.messageText || error.appendedText ? `${error.messageText} ${error.appendedText}` : null
      }))
    );
  }
}
